import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import {createRef} from '@servicenow/ui-renderer-snabbdom';
import moment from 'moment';
// component lifecycle actions
const {COMPONENT_DOM_READY} = actionTypes;

// constants
import {
	TIMELINE_ZOOM_OUT,
	TIMELINE_ZOOM_IN,
	TIMELINE_PREVIOUS_DATE,
	TIMELINE_NEXT_DATE,
	TIMELINE_CHART_ZOOM,
	TIMELINE_CHART_PAN,
	TIMELINE_CHART_TOGGLE_EVENT,
	TIMELINE_CHART_TOGGLE_RANGE,
	TIMELINE_CHART_RESET,
	TIMELINE_DO_CHART_RESET
} from '../common/timeline-constants';
import styles from './_sn-component-timeline.scss';

// external components
import '@servicenow/now-button';
import '../chart/timeline-chart';
import '../popover-button/timeline-popover-button';
import '../legend/timeline-legend';
import '../empty-state/timeline-empty-state';
import {t} from 'sn-translate';

const handleTimelineHook = (chartRef, updateState) => {
	updateState({chartRef: chartRef});
};

/** @seismicView sn-component-timeline */
const view = (state, {updateState}) => {
	const {
		dir,
		label,
		startDate,
		formattedStartDate,
		endDate,
		formattedEndDate,
		zoomStepPercent,
		panStepPercent,
		hidePanZoom,
		hideToggle,
		hideNow,
		legendEventTitle,
		legendRangeTitle,
		maxZoomMilli,
		visibleStart,
		visibleEnd,
		visibleEventData,
		visibleRangeData,
		isEmpty,
		isError,
		isLoading,
		errorMessage,
		emptyMessage,
		loadingMessage,
		disableLegend,
		legendTitle,
		isCompactView,
		manageSelections,
		selectedEvents,
		selectedRanges,
		highlightedEventIds
	} = state.properties;

	const {
		host,
		panPrevDisabled,
		panNextDisabled,
		zoomInDisabled,
		zoomOutDisabled,
		panDuration
	} = state;

	// TODO: MAKE SURE UNIQUE ID IS GETTING SET FOR A11Y
	const componentId =
		host && typeof host.getAttribute === 'function'
			? host.getAttribute('component-id')
			: '';
	const headerId = `${componentId}_timelineHeader`;

	// use to call instance method on chart
	const chartRef = createRef();

	// create attributes so buttons display disables state correctly
	const panPrevDisabledAttr = panPrevDisabled ? {disabled: true} : {};
	const panNextDisabledAttr = panNextDisabled ? {disabled: true} : {};
	const zoomInDisabledAttr = zoomInDisabled ? {disabled: true} : {};
	const zoomOutDisabledAttr = zoomOutDisabled ? {disabled: true} : {};

	// TODOL: use for empty state display
	// const hasEvents = visibleEventData && visibleEventData.length > 0;
	const hasRanges = visibleRangeData && visibleRangeData.length > 0;
	const eventsOnlyDisplay = !hasRanges;

	if (isEmpty) {
		return (
			<div className="timeline-layout now-p-block-end--lg now-p-inline--lg">
				<div className="hdr-wrap now-p-block--xs">
					<div id={headerId} className="hdr-text" aria-label={label}>
						{label}
					</div>
				</div>
				<timeline-empty-state isEmpty={isEmpty} message={emptyMessage} />
			</div>
		);
	}

	if (isError) {
		return (
			<div className="timeline-layout now-p-block-end--lg now-p-inline--lg">
				<div className="hdr-wrap now-p-block--xs">
					<div id={headerId} className="hdr-text" aria-label={label}>
						{label}
					</div>
				</div>
				<timeline-empty-state isError={isError} message={errorMessage} />
			</div>
		);
	}

	if (isLoading) {
		return (
			<div className="timeline-layout now-p-block-end--lg now-p-inline--lg">
				<div className="hdr-wrap now-p-block--xs">
					<div id={headerId} className="hdr-text" aria-label={label}>
						{label}
					</div>
				</div>
				<timeline-empty-state isLoading={isLoading} message={loadingMessage} />
			</div>
		);
	}

	// pan previous button display based on property
	const panPrevButton = hidePanZoom ? (
		''
	) : (
		<now-button
			{...panPrevDisabledAttr}
			bare
			icon-name="chevronLeftFill"
			className="pan-btns"
			size="sm"
			variant="secondary"
			tooltip-content={t('Go back in time')}
			config-aria={{
				'aria-label' : panDuration ? 
					t('Shift timeline scope back {0}', panDuration) 
					: t('Shift timeline scope back {0}%', panStepPercent)
			}}
			append-to-payload={{actionName: TIMELINE_PREVIOUS_DATE}}
		/>
	);

	// pan next button display based on property
	const panNextButton = hidePanZoom ? (
		''
	) : (
		<now-button
			{...panNextDisabledAttr}
			bare
			icon-name="chevronRightFill"
			className="pan-btns"
			size="sm"
			variant="secondary"
			tooltip-content={t('Go forward in time')}
			config-aria={{
				'aria-label' : panDuration ? 
					t('Shift timeline scope forward {0}', panDuration) 
					: t('Shift timeline scope forward {0}%', panStepPercent)
			}}
			append-to-payload={{actionName: TIMELINE_NEXT_DATE}}
		/>
	);

	// NOW button to reset timeline display
	const nowButton = hideNow ? (
		''
	) : (
		<now-button
			bare
			size="sm"
			className="now-p-inline-end--sm"
			variant="tertiary"
			label={t('Now')}
			tooltip-content={t('Now')}
			append-to-payload={{actionName: TIMELINE_CHART_RESET}}
		/>
	);

	// zoom buttons display based on property
	const zoomButtons = hidePanZoom ? (
		''
	) : (
		<div className="zoom-btns" aria-label={t('Zoom controls')}>
			<now-button
				bare
				{...zoomOutDisabledAttr}
				icon-name="magnifyingGlassMinusOutline"
				size="sm"
				variant="tertiary"
				className="zoom-btn-out"
				tooltip-content={t('Zoom out')}
				config-aria={{
					'aria-label': t(
						'Zoom out timeline scope {0} percent',
						zoomStepPercent
					)
				}}
				append-to-payload={{actionName: TIMELINE_ZOOM_OUT}}
			/>
			<now-button
				bare
				{...zoomInDisabledAttr}
				icon-name="magnifyingGlassPlusOutline"
				size="sm"
				variant="tertiary"
				tooltip-content={t('Zoom in')}
				config-aria={{
					'aria-label': t('Zoom in timeline scope {0} percent', zoomStepPercent)
				}}
				append-to-payload={{actionName: TIMELINE_ZOOM_IN}}
			/>
		</div>
	);

	const timelineChartLabel =
		isCompactView && label ? label : t('Timeline Chart');

	const detachableControls = isCompactView ? (
		<div className="now-align now-p-block-start--lg">
			{nowButton}
			{zoomButtons}
		</div>
	) : (
		<div className="hdr-wrap now-p-block--xxs">
			<div id={headerId} className="hdr-text" aria-label={label}>
				{label}
			</div>
			{nowButton}
			{zoomButtons}
		</div>
	);

	const legend = isCompactView ? null : (
		<div className="timeline-popover-wrap now-p-block-end--lg">
			<timeline-popover-button
				dir={dir}
				eventTitle={legendEventTitle}
				eventData={visibleEventData}
				rangeTitle={legendRangeTitle}
				rangeData={visibleRangeData}
				showLabel={t('Show {0}', legendTitle)}
				hideLabel={t('Hide {0}', legendTitle)}
				disableLegend={disableLegend}
				config-aria={{'aria-label': t('Toggle {0} display', legendTitle)}}>
				<div slot="popover-content">
					<timeline-legend
						dir={dir}
						eventTitle={legendEventTitle}
						eventData={visibleEventData}
						rangeTitle={legendRangeTitle}
						rangeData={visibleRangeData}
						hideToggle={hideToggle}
					/>
				</div>
			</timeline-popover-button>
		</div>
	);

	//for some unknown reason, if using `class`, it sometimes resolves to undefined.
	//converted to className to workaround the problem.
	const timelineWrapperClassName = `timeline-wrap now-p-block--lg ${
		eventsOnlyDisplay ? 'timeline-events-only' : ''
	}`;
	const timelineD3ClassName = `timeline-d3 ${
		hidePanZoom ? '' : 'now-p-inline--xs'
	}`;

	const getMinDate = (startDate, formattedStartDate) => {
		if (moment(formattedStartDate).isValid() && startDate == 0) {
			return moment(formattedStartDate).valueOf();
		}
		return startDate;
	};
	const getMaxDate = (endDate, formattedEndDate) => {
		if (moment(formattedEndDate).isValid() && endDate == 0) {
			return moment(formattedEndDate).valueOf();
		}
		return endDate;
	};

	// main component display
	return (
		<div className="timeline-layout now-p-block-end--lg now-p-inline--lg">
			{!isCompactView ? detachableControls : null}
			<div className={timelineWrapperClassName}>
				{panPrevButton}
				<div className={timelineD3ClassName} aria-label={timelineChartLabel}>
					<timeline-chart
						ref={chartRef}
						rtl={dir === 'rtl'}
						minDate={getMinDate(startDate, formattedStartDate)}
						maxDate={getMaxDate(endDate, formattedEndDate)}
						visibleStart={visibleStart}
						visibleEnd={visibleEnd}
						eventData={visibleEventData}
						rangeData={visibleRangeData}
						allowZoomPan={!hidePanZoom}
						zoomStepPercent={zoomStepPercent}
						panStepPercent={panStepPercent}
						maxZoomMilli={maxZoomMilli}
						manageSelections={manageSelections}
						selectedEvents={selectedEvents}
						selectedRanges={selectedRanges}
						isCompactView={isCompactView}
						highlightedEventIds={highlightedEventIds}
						hook-insert={() => handleTimelineHook(chartRef, updateState)}
					/>
				</div>
				{panNextButton}

				{isCompactView ? detachableControls : null}
			</div>
			{legend}
		</div>
	);
};

/**
 * Displays events in chronological order to show complex data in a high-level view. 
 * A timeline can include event indicators and/or range indicators and can be either interactive or show a fixed view of time and events.
 *
 *
 * ```jsx
 * const events = [
 *   {name: 'critical', icon: 'circleExclamationFill', color: '#ff0000', label: 'Critical event', labelPlural: 'Critical events', events: [
 *     {timestamp: 1549111622099, description: 'Critical event #1', data: {id: 'id_1'}},
 *     {timestamp: 1549344808283, description: 'Critical event #2, data: {id: 'id_2'}}
 *   ]},
 *   {name: 'minor', icon: 'circleExclamationFill', color: '#fcc742', label: 'Minor event', labelPlural: 'Minor events', events: [
 *     {timestamp: 1552188356453, description: 'Minor event #1', data: {id: 'id_1'}},
 *     {timestamp: 1547477675493, description: 'Minor event #2, data: {id: 'id_2'}},
 *     {timestamp: 1547334241375, description: 'Minor event #3, data: {id: 'id_3'}}
 *   ]}
 * ];
 *
 * const ranges = [
 *   {name: 'RangeA', color: '#F95070', label: 'Range type A', ranges: [
 *     {start: 1549152000000, end: 1549497600000, description: 'Range type A #1', data: {id: 'id_1'}},
 *     {start: 1549584000000, end: 1550275200000, description: 'Range type A #2', data: {id: 'id_2'}}
 *   ]},
 *   {name: 'RangeB', color: '#5C94D6', label: 'Range type B', ranges: [
 *     {start: 1550275200000, end: 1550534400000, description: 'Range type B #1', data: {id: 'id_3'}},
 *     {start: 1550707200000, end: 1551139200000, description: 'Range type B #2', data: {id: 'id_4'}}
 *   ]}
 * ];
 *
 * <sn-component-timeline label="Timeline of Events" startDate={1546318800000} endDate={1554091200000} eventData={events}></sn-component-timeline>
 * <sn-component-timeline label="Timeline of Ranges" startDate={1546318800000} endDate={1554091200000} displayStartDate={1548910800000} displayEndDate={1552194000000} rangeData={ranges}></sn-component-timeline>
 * <sn-component-timeline
 *   label="Advanced Timeline"
 *   startDate={1546318800000}
 *   endDate={1554091200000}
 *   displayStartDate={1548910800000}
 *   displayEndDate={1552194000000}
 *   eventData={events}
 *   rangeData={ranges}
 *   zoomStepPercent={25}
 *   minZoomSpread="MN"
 *   panStepPercent={10}
 *   legendEventTitle="Advanced Timeline Events"
 *   legendRangeTitle="Advanced Timeline Ranges"
 *   hide-pan-zoom
 *	 hide-toggle
 * </sn-component-timeline>
 * ```
 *
 * @seismicElement sn-component-timeline
 * @summary Timeline for displaying event data in chronological order.
 */
createCustomElement('sn-component-timeline', {
	styles,
	renderer: {
		view: view
	},
	initialState: {
		host: {},
		panPrevDisabled: false,
		panNextDisabled: false,
		zoomInDisabled: false,
		zoomOutDisabled: false,
		chartRef: false
	},
	properties: {
		/**
		 * Header text for the timeline. Requires localization from the consuming component.
		 * @type {string}
		 */
		label: {
			default: ''
		},
		/**
		 * The minimum date that can be displayed in the timeline as a UTC timestamp.
		 * @type {number}
		 */
		startDate: {
			default: 0
		},
		/**
		 * The minimum date that can be displayed in the timeline as a formatted string.
		 * Supports all date format strings that the `moment.format()` function supports. For example: "2020-06-22 21:00:00".
		 * @type {'YYYY-MM-DD HH:mm:SS'}
		 */
		formattedStartDate: {
			default: ''
		},
		/**
		 * The maximum date that can be displayed in the timeline as a UTC timestamp.
		 * @type {number}
		 */
		endDate: {
			default: 0
		},
		/**
		 * The maximum date that can be displayed in the timeline as formatted string.
		 * Supports all date format strings that the `moment.format()` function supports. For example: "2020-06-22 21:00:00".
		 * @type {'YYYY-MM-DD HH:mm:SS'}
		 */
		formattedEndDate: {
			default: ''
		},
		/**
		 * Optional starting date displayed on the timeline as a UTC timestamp.
		 * By default, the value of the `startDate` property is used.
		 * @type {number}
		 */
		displayStartDate: {
			default: -1
		},
		/**
		 * Optional starting date displayed on the timeline as a formatted string.
		 * Supports all date format strings that the `moment.format()` function supports. For example: "2020-06-22 21:00:00".
		 * @type {'YYYY-MM-DD HH:mm:SS'}
		 */
		formattedDisplayStartDate: {
			default: ''
		},
		/**
		 * Optional ending date displayed on the timeline as a UTC timestamp.
		 * By default, the value of the `endDate` property is used.
		 * @type {number}
		 */
		displayEndDate: {
			default: -1
		},
		/**
		 * Optional ending date displayed on the timeline as a formatted string.
		 * Supports all date format strings that the `moment.format()` function supports. For example: "2020-06-22 21:00:00".
		 * @type {'YYYY-MM-DD HH:mm:SS'}
		 */
		formattedDisplayEndDate: {
			default: ''
		},
		/**
		 * The percentage used to calculate the amount of zoom for each zoom action.
		 * @type {number}
		 */
		zoomStepPercent: {
			default: 20
		},
		/**
		 * The minimum display range allowed for the zoom. Values include: `Y`: Year, `M`: Month, `D`: Day, `H`: Hour, `MN`: Minute, `S`: Second, `MS`: Millisecond.
		 * Default: Day.
		 * @type {('Y'|'M'|'D'|'H'|'MN'|'S'|'MS')}
		 */
		minZoomSpread: {
			default: 'D'
		},
		/**
		 * When true, hides the pan and zoom buttons from the timeline display.
		 * @type {boolean}
		 */
		hidePanZoom: {
			reflect: true,
			default: false
		},
		/**
		 * When true, hides the toggle from the timeline legend display.
		 * @type {boolean}
		 */
		hideToggle: {
			default: false
		},
		legendTitle: {
			default: 'Legend'
		},
		/**
		 * When true, hides the now-button from the timeline display.
		 * @type {boolean}
		 */
		hideNow: {
			default: true
		},
		/**
		 * The percentage used to calculate the amount of pan for each pan action.
		 * @type {number}
		 */
		panStepPercent: {
			default: 20
		},
		/**
		 * The title used for events in the legend. Requires localization from the consuming component.
		 * @type {string}
		 */
		legendEventTitle: {
			default: t('Timeline indicators')
		},
		/**
		 * The title used for ranges in the legend. Requires localization from the consuming component.
		 * @type {string}
		 */
		legendRangeTitle: {
			default: t('Timeline ranges')
		},
		/**
		 * Object array that defines the events to be displayed on the timeline.
		 * Each object must have a unique name.
		 * The icon must be from the now-icon gallery.
		 * The color used for the icon must be a full hex string.
		 * The data object can be any valid JSON specific to the particular event.
		 * The label, labelPlural, and description values require localization from the consuming component.
		 *
		 * Configure the events object using the following keys:
		 * - `timestamp`: Number. UTC timestamp for the event. Required.
		 * - `description`: String. Short text description for the event. Required.
		 * - `data`: Object. Any valid JSON object that can be associated with the particular event.
		 * @type {{name: string, icon: string, color: string, label: string, labelPlural: string, events: Object[]}[]}
		 */
		eventData: {
			default: [],
			cast: 'array'
		},
		/**
		 * Object array that defines the ranges to be displayed on the timeline.
		 * Each object must have a unique name.
		 * The color used for the range must be a full hex string.
		 * The data object can be any valid JSON specific to the particular range.
		 * The label and description values require localization from the consuming component.
		 *
		 * Configure the ranges object using the following keys:
		 * - `start`: Number. Beginning date for the range as a UTC timestamp. Required.
		 * - `end`: Number. Ending date for the range as a UTC timestamp. Required.
		 * - `description`: String. Short text description for the range. Required.
		 * - `data`: Object. Any valid JSON object that can be associated with the particular range.
		 * @type {{name: string, color: string, label: string, ranges: Object[]}[]}
		 */
		rangeData: {
			default: [],
			cast: 'array'
		},
		/**
		 * When true, the component is in an empty state.
		 * @type {boolean}
		 */
		isEmpty: {
			default: false
		},
		/**
		 * Default message to show in an empty state.
		 * @type {string}
		 */
		emptyMessage: {
			default: t('Contact admin for more information')
		},
		/**
		 * When true, the component is in an error state.
		 * @type {boolean}
		 */
		isError: {
			default: false
		},
		/**
		 * Default error message.
		 * @type {string}
		 */
		errorMessage: {
			default: t('This content could not be loaded')
		},
		/**
		 * When true, the component is loading.
		 * @type {boolean}
		 */
		isLoading: {
			default: false
		},
		/**
		 * Default loading message.
		 * @type {string}
		 */
		loadingMessage: {
			default: t('Loading timeline data')
		},

		/**
		 * When true, the timeline displays on one line. The legend and title do not display.
		 * @type {boolean}
		 */
		isCompactView: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},

		/**
		 * When true, clicking a range or event only dispatches the action `TIMELINE_CHART#DATA_SELECTED`.
		 * The actual selection is performed from input properties `selectedEvents` and `selectedRanges`.
		 *
		 * This property requires each event and each range to have a unique ID field: `range.data.id` and `event.data.id`.
		 * Ensure that the ID is unique within each ranges and events array.
		 * @type {boolean}
		 */
		manageSelections: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},

		/**
		 * This property is active only when `manageSelections` is true.
		 * The format is an array of timeline event objects in the same format as provided by action `TIMELINE_CHART#DATA_SELECTED`.
		 * All events in this array are selected on the timeline.
		 * @type {array}
		 */
		selectedEvents: {
			default: [],
			schema: {
				type: 'array'
			}
		},

		/**
		 * This property is active only when `manageSelections=true`.
		 * The format is an array of timeline range objects in the same format as provided by action `TIMELINE_CHART#DATA_SELECTED`.
		 * All ranges in this array are selected on the timeline.
		 * @type {array}
		 */
		selectedRanges: {
			default: [],
			schema: {
				type: 'array'
			}
		},

		/**
		 * Array of IDs of events to highlight on the tiemline.
		 * Highlighting is only supported for events.
		 * A highlighted event must have a unique ID in `event.data.id`.
		 * @type {array}
		 */
		highlightedEventIds: {
			default: [],
			schema: {
				type: 'array'
			}
		},

		/** @private */
		disableLegend: {
			computed({properties: {eventData, rangeData}}) {
				if (!eventData || !rangeData) {
					return;
				}
				return eventData.length == 0 && rangeData.length == 0;
			}
		},
		/** @private */
		visibleEventData: {
			computed({
				properties: {
					eventData,
					startDate,
					formattedStartDate,
					endDate,
					formattedEndDate
				}
			}) {
				// filter events if they fall outside the minDate and maxDate (e.g. startDate and endDate)
				// Note: filter is happening here because legend component needs to match chart component
				// otherwise it could happen on the chart component
				if (!eventData) {
					return;
				}

				if (moment(formattedStartDate).isValid() && startDate == 0) {
					startDate = moment(formattedStartDate).valueOf();
				}
				if (moment(formattedEndDate).isValid() && endDate == 0) {
					endDate = moment(formattedEndDate).valueOf();
				}

				const visibleEventData = eventData
					.map((eventTypeObj) => {
						eventTypeObj.events = eventTypeObj.events.filter((event) => {
							return event.timestamp >= startDate && event.timestamp <= endDate;
						});

						return eventTypeObj;
					})
					.filter((eventTypeObj) => {
						return eventTypeObj.events.length > 0;
					});

				return visibleEventData;
			}
		},
		/** @private */
		visibleRangeData: {
			computed({
				properties: {
					rangeData,
					startDate,
					formattedStartDate,
					endDate,
					formattedEndDate
				}
			}) {
				// filter ranges if they fall outside the minDate and maxDate (e.g. startDate and endDate)
				// Note: filter is happening here because legend component needs to match chart component
				// otherwise it could happen on the chart component
				if (!rangeData) {
					return;
				}

				if (moment(formattedStartDate).isValid() && startDate == 0) {
					startDate = moment(formattedStartDate).valueOf();
				}
				if (moment(formattedEndDate).isValid() && endDate == 0) {
					endDate = moment(formattedEndDate).valueOf();
				}

				const visibleRangeData = rangeData
					.map((rangeTypeObj) => {
						rangeTypeObj.ranges = rangeTypeObj.ranges.filter((range) => {
							return range.start >= startDate && range.end <= endDate;
						});

						return rangeTypeObj;
					})
					.filter((rangeTypeObj) => {
						return rangeTypeObj.ranges.length > 0;
					});

				return visibleRangeData;
			}
		},
		/** @private */
		visibleStart: {
			computed({
				properties: {
					startDate,
					formattedStartDate,
					displayStartDate,
					formattedDisplayStartDate,
					hidePanZoom
				}
			}) {
				if (moment(formattedStartDate).isValid() && startDate == 0) {
					startDate = moment(formattedStartDate).valueOf();
				}
				if (
					moment(formattedDisplayStartDate).isValid() &&
					displayStartDate == -1
				) {
					displayStartDate = moment(formattedDisplayStartDate).valueOf();
				}

				return displayStartDate < 0 || hidePanZoom
					? startDate
					: displayStartDate;
			}
		},
		/** @private */
		visibleEnd: {
			computed({
				properties: {
					endDate,
					formattedEndDate,
					displayEndDate,
					formattedDisplayEndDate,
					hidePanZoom
				}
			}) {
				if (moment(formattedEndDate).isValid() && endDate == 0) {
					endDate = moment(formattedEndDate).valueOf();
				}
				if (moment(formattedDisplayEndDate).isValid() && displayEndDate == -1) {
					displayEndDate = moment(formattedDisplayEndDate).valueOf();
				}

				return displayEndDate < 0 || hidePanZoom ? endDate : displayEndDate;
			}
		},

		/** @private */
		maxZoomMilli: {
			computed({properties: {minZoomSpread}}) {
				if (!minZoomSpread) {
					return;
				}

				const spread = minZoomSpread ? minZoomSpread.toUpperCase() : 'H';
				switch (spread) {
					case 'Y': // Year
						return 31536000000;
					case 'M': // Month
						return 2678400000;
					case 'W': // Week
						return 604800000;
					case 'D': // Day
						return 86400000;
					case 'MN': // Minute
						return 60000;
					case 'S': // Second
						return 1000;
					case 'MS': // Millisecond
						return 1;
					case 'H': // Hour - default
					default:
						return 3600000;
				}
			}
		}
	},
	actions: {
		/**
		 * Dispatched any time an event or range is selected on the timeline.
		 *
		 * The selectedEvents are configured with the following keys:
		 * - `id`: String. ID for the event.
		 * - `eventName`: String. Event name for the type of event. This is taken from the `name` key of the parent `eventData` object.
		 * - `timestamp`: Number. UTC timestamp for the event.
		 * - `description`: String. Short text description for the event.
		 * - `data`: Object. Any valid JSON object that can be associated with the particular event.
		 *
		 * The selectedRanges are configured with the following keys:
		 * - `id`: String. ID for the event
		 * - `rangeName`: String. Range name for the type of range. This is taken from the `name` key of the parent `rangeData` object.
		 * - `start`: Number. Beginning date for the range as a UTC timestamp.
		 * - `end`: Number. Ending date for the range as a UTC timestamp.
		 * - `description`: String. Short text description for the range.
		 * - `data`: Object. Any valid JSON object that can be associated with the particular range.
		 * @type {{selectedEvents: (Object[]|[]), selectedRanges: (Object[]|[])}}
		 */
		'TIMELINE_CHART#DATA_SELECTED': {
			schema: {
				type: 'object',
				properties: {
					selectedEvents: {
						type: 'array',
						items: {
							type: 'object',
							properties: {
								id: {type: 'string'},
								eventName: {type: 'string'},
								timestamp: {type: 'number'},
								description: {type: 'string'},
								data: {type: 'object'}
							}
						}
					},
					selectedRanges: {
						type: 'array',
						items: {
							type: 'object',
							properties: {
								id: {type: 'string'},
								rangeName: {type: 'string'},
								start: {type: 'number'},
								end: {type: 'number'},
								description: {type: 'string'},
								data: {type: 'object'}
							}
						}
					}
				}
			}
		}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]({host, updateState}) {
			// We need access to host in our action handlers and this is the best way to get that right now given limitations of Seismic
			updateState({host: host});
		},
		'NOW_BUTTON#CLICKED': {
			private: true,
			stopPropagation: true,
			effect: ({action, dispatch}) => {
				// run the actual handler for the button that was selected
				dispatch(action.payload.actionName);
			}
		},
		TIMELINE_CHART_RESET: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				state.chartRef.current.dispatch(TIMELINE_DO_CHART_RESET);
			}
		},
		TIMELINE_CHART_CALCULATION: {
			private: true,
			stopPropagation: true,
			effect: ({action, updateState}) => {
				updateState({
					panDuration: action.payload.panDuration
				});
			}
		},
		TIMELINE_ZOOM_IN: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				state.chartRef.current.dispatch(TIMELINE_CHART_ZOOM, {
					dir: TIMELINE_ZOOM_IN,
					step: state.properties.zoomStepPercent
				});
			}
		},
		TIMELINE_ZOOM_OUT: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				state.chartRef.current.dispatch(TIMELINE_CHART_ZOOM, {
					dir: TIMELINE_ZOOM_OUT,
					step: state.properties.zoomStepPercent
				});
			}
		},
		TIMELINE_PREVIOUS_DATE: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				state.chartRef.current.dispatch(TIMELINE_CHART_PAN, {
					dir: TIMELINE_PREVIOUS_DATE,
					step: state.properties.panStepPercent
				});
			}
		},
		TIMELINE_NEXT_DATE: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				state.chartRef.current.dispatch(TIMELINE_CHART_PAN, {
					dir: TIMELINE_NEXT_DATE,
					step: state.properties.panStepPercent
				});
			}
		},
		'TIMELINE_LEGEND#LEGEND_TOGGLED': {
			stopPropagation: true,
			effect: ({state, action}) => {
				const {item, show, dataType} = action.payload;

				state.chartRef.current.dispatch(
					dataType === 'range'
						? TIMELINE_CHART_TOGGLE_RANGE
						: TIMELINE_CHART_TOGGLE_EVENT,
					{
						name: item.name,
						visible: show
					}
				);
			}
		},
		'TIMELINE_CHART#PAN_ZOOM': {
			stopPropagation: true,
			effect: ({action, updateState}) => {
				const {zoomIn, zoomOut, panPrev, panNext} = action.payload;

				updateState({
					panNextDisabled: !panNext,
					panPrevDisabled: !panPrev,
					zoomInDisabled: !zoomIn,
					zoomOutDisabled: !zoomOut
				});
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched any time an event or range is selected on the timeline.
		 *
		 * The selectedEvents are configured with the following keys:
		 * - `eventName`: String. Event name for the type of event. This is taken from the `name` key of the parent `eventData` object.
		 * - `timestamp`: Number. UTC timestamp for the event.
		 * - `description`: String. Short text description for the event.
		 * - `data`: Object. Any valid JSON object that can be associated with the particular event.
		 *
		 * The selectedRanges are configured with the following keys:
		 * - `rangeName`: String. Range name for the type of range. This is taken from the `name` key of the parent `rangeData` object.
		 * - `start`: Number. Beginning date for the range as a UTC timestamp.
		 * - `end`: Number. Ending date for the range as a UTC timestamp.
		 * - `description`: String. Short text description for the range.
		 * - `data`: Object. Any valid JSON object that can be associated with the particular range.
		 * @type {{selectedEvents: (Object[]|[]), selectedRanges: (Object[]|[])}}
		 */
		'TIMELINE_CHART#DATA_SELECTED': {},

		/**
		 * Dispatched on every change in the display range using the `displayStartDate` and `displayEndDate` properties
		 * due to a user panning or zooming.
		 * Provides the timestamps of the updated display range.
		 * Sometimes this action dispatches multiple times during a single drag/zoom operation, providing intermediate values of the changing range.
		 * Use debouncing on the receiving side to optimize the action handler.
		 * - `currentViewStart`: Number. Beginning date for the updated display range as a UTC timestamp.
		 * - `currentViewEnd`: Number. Ending date for the updated display range as a UTC timestamp.
		 */
		'TIMELINE_CHART#RANGE_CHANGE': {},

		/**
		 * Dispatched after new time range is provided using the `displayStartDate` and `displayEndDate` properties
		 * when the timeline visualisation is synchronized on the new range.
		 */
		'TIMELINE_CHART#INITIALIZED': {}
	}
});
