import {createCustomElement} from '@servicenow/ui-core';
/* eslint-disable-next-line no-unused-vars */
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import map from 'lodash/map';
import styles from './_timeline-legend.scss';

// external components
import './timeline-legend-title';
import './timeline-legend-item';
import {t} from 'sn-translate';

/** @seismicView timeline-legend */
const view = (state) => {
	// props
	const {
		dir,
		eventTitle,
		eventData,
		rangeTitle,
		rangeData,
		hideToggle
	} = state.properties;

	const eventLegend =
		eventData.length > 0 ? (
			<Fragment>
				<timeline-legend-title dir={dir} legendTitle={eventTitle} />
				<div className="legend-list" role="rowgroup">
					{map(eventData, (item, index) => (
						<timeline-legend-item
							dir={dir}
							item={item}
							index={index}
							hideToggle={hideToggle}
						/>
					))}
				</div>
			</Fragment>
		) : (
			''
		);

	const rangeLegend =
		rangeData.length > 0 ? (
			<Fragment>
				<timeline-legend-title dir={dir} legendTitle={rangeTitle} />
				<div className="legend-list" role="rowgroup">
					{map(rangeData, (item, index) => (
						<timeline-legend-item
							dir={dir}
							item={item}
							index={index}
							hideToggle={hideToggle}
							is-range-item
						/>
					))}
				</div>
			</Fragment>
		) : (
			''
		);

	// main component display
	return (
		<div className="legend-layout">
			<div className="now-p-block-end--md">{eventLegend}</div>
			<div className="now-p-block-start--sm">{rangeLegend}</div>
		</div>
	);
};

/**
 * Displays chart information for the timeline component in a formatted legend and allows for toggling the display of items on the timeline.
 *
 * ```jsx
 * const events = [
 *   {name: 'critical', icon: 'circleExclamationFill', color: ''#ff0000', label: 'Critical event', labelPlural: 'Critical events', events: [
 *     {timestamp: 1549111622099, description: 'Critical event #1', data: {}},
 *     {timestamp: 1549344808283, description: 'Critical event #2, data: {}}
 *   ]},
 *   {name: 'minor', icon: 'circleExclamationFill', color: ''#fcc742', label: 'Minor event', labelPlural: 'Minor events', events: [
 *     {timestamp: 1552188356453, description: 'Minor event #1', data: {}},
 *     {timestamp: 1547477675493, description: 'Minor event #2, data: {}},
 *     {timestamp: 1547334241375, description: 'Minor event #3, data: {}}
 *   ]}
 * ];
 *
 * const ranges = [
 * 	 {name: 'RangeA', color: '#F95070', label: 'Range type A', ranges: [
 *     {start: 1549152000000, end: 1549497600000, description: 'Range type A #1', data: {}},
 *     {start: 1549584000000, end: 1550275200000, description: 'Range type A #2', data: {}}
 *   ]},
 * 	 {name: 'RangeB', color: '#5C94D6', label: 'Range type B', ranges: [
 *     {start: 1550275200000, end: 1550534400000, description: 'Range type B #1', data: {}},
 *     {start: 1550707200000, end: 1551139200000, description: 'Range type B #2', data: {}}
 *   ]}
 * ];
 *
 * <timeline-legend
 * 	 eventTitle="Timeline events"
 * 	 eventData={events}
 * 	 rangeTitle="Timeline ranges"
 * 	 rangeData={ranges}
 * </timeline-legend>
 * ```
 *
 * @seismicElement timeline-legend
 * @summary Displays chart information for the timeline component in a formatted legend.
 */
createCustomElement('timeline-legend', {
	styles,
	renderer: {
		view: view
	},
	properties: {
		/**
		 * The title used for events in the legend.
		 * @type {string}
		 */
		eventTitle: {
			default: t('Timeline indicators')
		},
		/**
		 * Object array that defines the events to be displayed on the legend.
		 * Each object must have a unique name.
		 * The icon must be taken from the gallery of available eds icons.
		 * The color used for the icon must be a full hex string.
		 * The data object can be any valid json specific to the particular event.
		 *
		 * The events objects are configured with the following keys:
		 * - `timestamp: number` required - UTC timestamp for the event
		 * - `description: string` required - Short text description for the event
		 * - `data: object` - Any valid json object that can be associated with the particular event
		 * @type {{name: string, icon: string, color: string, label: string, labelPlural: string, events: Object[]}[]}
		 */
		eventData: {
			default: [],
			cast: 'array'
		},
		/**
		 * The title used for ranges in the legend.
		 * @type {string}
		 */
		rangeTitle: {
			default: t('Timeline ranges')
		},
		/**
		 * Object array that defines the ranges to be displayed on the legend.
		 * Each object must have a unique name.
		 * The color used for the range must be a full hex string.
		 * The data object can be any valid json specific to the particular range.
		 *
		 * The ranges objects are configured with the following keys:
		 * - `start: number` required - Beginning date for the range as a UTC timestamp
		 * - `end: number` required - Ending date for the range as a UTC timestamp
		 * - `description: string` required - Short text description for the range
		 * - `data: object` - Any valid json object that can be associated with the particular range
		 * @type {{name: string, color: string, label: string, ranges: Object[]}[]}
		 */
		rangeData: {
			default: [],
			cast: 'array'
		},
		/**
		 * Disables the toggle from the timeline legend display.
		 * @type {boolean}
		 */
		hideToggle: {
			default: false
		}
	}
});
