import {
	TIMELINE_CHART_DATA_SELECTED,
	TIMELINE_CHART_STATIC_RENDER
} from '../common/timeline-constants';

import {getItemId} from '../common/utils';

/**
 * This is a logic for manageSelections=false.
 * This logic doesn't rely on unique ids for items and sets 'selected' flag directly on current items.
 */
const unmanageableItemSelected = ({
	currentTypeItems,
	otherTypeItems,
	item,
	d3Event,
	dispatch
}) => {
	const nextSelectedState = !item.selected;

	// unselect all events
	otherTypeItems.forEach((it) => (it.selected = false));

	// if ctrl key and command key are not pressed then unselect all events
	if (!d3Event.metaKey && !d3Event.ctrlKey) {
		currentTypeItems.forEach((it) => (it.selected = false));
	}

	item.selected = nextSelectedState;
	if (item.groupEvents) {
		item.groupEvents.forEach((it) => (it.selected = nextSelectedState));
	}

	const selectedItems = currentTypeItems.filter((it) => it.selected);

	// redraw the chart
	dispatch(TIMELINE_CHART_STATIC_RENDER);

	return selectedItems;
};

export const applySelectedItems = (items, selectedItems) => {
	const selectedLookup = selectedItems.reduce((map, it) => {
		map.add(getItemId(it) || {});
		return map;
	}, new Set());

	items.forEach((it) => (it.selected = selectedLookup.has(getItemId(it))));
};

const formatSelectedEvent = (event) => ({
	eventName: event.name,
	timestamp: event.timestamp,
	description: event.description,
	data: event.data,
	id: event.id
});

const formatSelectedRange = (range) => ({
	rangeName: range.name,
	start: range.start,
	end: range.end,
	description: range.description,
	data: range.data,
	id: range.id
});

/**
 * Prepares arrays of selectedEvents and selectedRanges, and dispatches them with `TIMELINE_CHART_DATA_SELECTED`.
 * If `maangeSelections=false`, a fallback logic involed, that also updates `selected` flags on items and re-renders the timeline.
 * @param {Object} param0
 *  item {Object} - currently clicked item (range or event). Expected to have proeprty `type={range|event}`.
 *  ranges {Array} - flat array of timeline ranges from the chart state.
 *  events {Array} - flat array of timeline events from the chart state.
 *  d3Event {Object} - D3 event object, to trace state of meta keys during the operation.
 *  dispatch {Function}
 *  isManageable {Boolean} - value of `manage-selections` property.
 */
const selectItem = ({
	item,
	ranges,
	events,
	d3Event,
	dispatch,
	isManageable
}) => {
	const currentTypeItems =
		item.type === 'event' ? events : item.type === 'range' ? ranges : null;

	if (!currentTypeItems) {
		return;
	}

	let newSelectedItems;

	if (!isManageable) {
		const otherTypeItems = currentTypeItems === ranges ? events : ranges;
		//modifying 'selected' flags directly, not involving mapping of items by id.
		newSelectedItems = unmanageableItemSelected({
			currentTypeItems,
			otherTypeItems,
			item,
			d3Event,
			dispatch
		});
	} else {
		//relying on unique ids to keep track of selected items.
		const selectedItems =
			!d3Event || (!d3Event.metaKey && !d3Event.ctrlKey)
				? []
				: currentTypeItems.filter((it) => it.selected);

		const nextSelectedState = !item.selected;
		const itemId = getItemId(item);

		newSelectedItems = nextSelectedState
			? [...selectedItems, item, ...(item.groupEvents || [])]
			: selectedItems.filter((it) => getItemId(it) !== itemId);
	}

	const payload = {
		selectedEvents:
			currentTypeItems === ranges
				? []
				: newSelectedItems.map(formatSelectedEvent),

		selectedRanges:
			currentTypeItems === events
				? []
				: newSelectedItems.map(formatSelectedRange)
	};

	//pass up the currently selected data
	dispatch(TIMELINE_CHART_DATA_SELECTED, payload);
};

/**
 * Generic effect for click on event and range o nthe timeline.
 * Handles actions `TIMELINE_CHART_EVENT_SELECT` and `TIMELINE_CHART_RANGE_SELECT.
 * Supports both manageSelections modes (true and false)
 */
export const effectSelectItems = (coeffects) => {
	const {action, state, dispatch, properties} = coeffects;
	const {events, ranges} = state;
	const {d3Evt: d3Event, evt: item} = action.payload;
	const {manageSelections: isManageable = false} = properties;

	selectItem({item, ranges, events, d3Event, dispatch, isManageable});
};
