import {createCustomElement} from '@servicenow/ui-core';
import {createRef} from '@servicenow/ui-renderer-snabbdom';

// constants
import {
	TIMELINE_SHOW_POPOVER,
	TIMELINE_HIDE_POPOVER,
	TIMELINE_POPOVER_ESC_TAB_PRESS
} from '../common/timeline-constants';
import styles from './_timeline-popover-button.scss';
import {getRangeDisplay} from '../utilities/ultilities';

// external components
import map from 'lodash/map';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import keyBindingBehavior, {
	findTabbableNodes
} from '@servicenow/behavior-key-binding';
import {t} from 'sn-translate';

const buttonTargetRef = 'nowButtonRef';
const popoverTargetRef = 'popoverContainer';
let outsideClickListener;
let lastFocusedElementLocal;

const removeClickListener = () => {
	document.removeEventListener('click', outsideClickListener);
};

const addClickListener = () => {
	document.addEventListener('click', outsideClickListener);
};

const elementIsContained = (targetRef, element, event) => {
	let contained = false;
	const evtPath = event.path || (event.composedPath && event.composedPath());

	// search the elements DOM path to see if it's contained within the popover slot
	if (evtPath) {
		// Chrome
		const foundPath = evtPath.filter((path) => {
			return path.targetRef === targetRef;
		});

		contained = foundPath.length > 0;
	} else {
		// did the click happen inside the bounds of the parent element rectangle?
		// Safari and FireFox
		const domRect = element.getBoundingClientRect();

		if (domRect.x && domRect.y) {
			contained =
				domRect.x < event.clientX &&
				event.clientX < domRect.right &&
				domRect.y < event.clientY &&
				event.clientY < domRect.bottom;
		} else {
			// Edge
			// Note: event.target seems to be the entire sn-component-timeline
			const currentElement = event.srcElement || event.target;
			const elmRect = currentElement.getBoundingClientRect();

			contained =
				elmRect.left >= domRect.left &&
				elmRect.right <= domRect.right &&
				elmRect.top >= domRect.top &&
				elmRect.bottom <= domRect.bottom;
		}
	}

	return contained;
};

// handle click outside of opened popover
const hideOnClickOutside = (buttonElement, popoverElement, dispatch) => {
	outsideClickListener = (event) => {
		const buttonContained = elementIsContained(
			buttonTargetRef,
			buttonElement,
			event
		);

		// the button click handler will fire after the outside click handler.
		// let the button click handler do the right thing
		if (buttonContained) {
			setTimeout(() => {
				removeClickListener();

				// use dispatch here so properties are updated and css is set correctly
				dispatch(TIMELINE_HIDE_POPOVER);
			}, 50);

			return;
		}

		const popoverContained = elementIsContained(
			popoverTargetRef,
			popoverElement,
			event
		);

		if (!popoverContained) {
			removeClickListener();

			// use dispatch here so properties are updated and css is set correctly
			dispatch(TIMELINE_HIDE_POPOVER);
		}
	};

	addClickListener();
};

// Note: Modified version of code from eds-modal
const findFocusableEls = (host) => {
	const tabbableNodes = findTabbableNodes(host);
	const firstFocusableElement = tabbableNodes[0];
	const lastFocusableElement = tabbableNodes[tabbableNodes.length - 2];
	const decoyElement = tabbableNodes[tabbableNodes.length - 1]; // We never want to actually focus on this element, it is just here to ensure our focus trap isn't broken by navigating to the last focusable element on the page.
	return {
		tabbableNodes,
		firstFocusableElement,
		lastFocusableElement,
		decoyElement
	};
};

/**
 * Closes the popover when the user presses the Escape key.
 */
const handleEscapePress = (host, evt, dispatch, isShown) => {
	// don't trap esc if the popover is closed
	if (!isShown) {
		return;
	}

	evt.preventDefault();

	// clean up listener if it exists.
	// this resolves an issue where the popover can get stuck in a closed state
	removeClickListener();

	dispatch(TIMELINE_HIDE_POPOVER);
};

/**
 * Traps user focus when the user presses the Tab key.
 */
const handleTabPress = (host, evt, isShown) => {
	// don't trap tab if the popover is closed
	if (!isShown) {
		return;
	}

	const {
		tabbableNodes,
		firstFocusableElement,
		lastFocusableElement,
		decoyElement
	} = findFocusableEls(host);
	// When opening the popover now, focus is automatically set on the popover button which is the firstFocusableElement.
	const evtPath = evt.path || (evt.composedPath && evt.composedPath());
	let elementToFocus;

	// Firefox doesn't seem to follow the same data for event path. Use originalTarget instead
	let lastFocusedElement =
		evtPath.length === 0 ? evt.originalTarget : evtPath[0];

	// Safari doesn't give event path or originalTarget. Attempt to use local variable to track last element that was focused
	// Note: Safari users will still have to enable "Press Tab to highlight each item on a webpage" in preferences for this to work
	if (!lastFocusedElement) {
		lastFocusedElement = lastFocusedElementLocal || firstFocusableElement;
	}

	evt.preventDefault();

	if (evt.shiftKey) {
		/**
		 * When the user tabs backward past the first focusable element, focus
		 * the last focusable element
		 */
		const lastFocusedElementIndex = tabbableNodes.indexOf(lastFocusedElement);
		const previousFocusableElement = tabbableNodes[lastFocusedElementIndex - 1];
		if (previousFocusableElement && previousFocusableElement !== decoyElement) {
			elementToFocus = previousFocusableElement;
		} else {
			elementToFocus = lastFocusableElement;
		}
	} else {
		/**
		 * When the user tabs forward past the last focusable element, focus the
		 * first focusable element
		 */
		const lastFocusedElementIndex = tabbableNodes.indexOf(lastFocusedElement);
		const nextFocusableElement = tabbableNodes[lastFocusedElementIndex + 1];

		if (nextFocusableElement && nextFocusableElement !== decoyElement) {
			elementToFocus = nextFocusableElement;
		} else {
			elementToFocus = firstFocusableElement;
		}
	}

	if (elementToFocus) {
		elementToFocus.focus();
		lastFocusedElementLocal = elementToFocus;
	}
};

/** @seismicView timeline-popover */
const view = (state) => {
	// props
	const {
		showLabel,
		hideLabel,
		fullAriaConfig,
		showPopover,
		disableLegend
	} = state.properties;
	const buttonRef = createRef();
	const popoverRef = createRef();

	// main component view
	return (
		<div className="timeline-popover-layout">
			<now-button
				ref={buttonRef}
				target-ref={buttonTargetRef}
				bare
				disabled={disableLegend}
				className="popover-eds-button"
				size="sm"
				variant="tertiary"
				config-aria={fullAriaConfig}
				append-to-payload={{buttonRef: buttonRef, popoverRef: popoverRef}}>
				<slot>
					{showPopover ? hideLabel : showLabel}
					<now-icon
						icon="caret-down-fill"
						size="sm"
						className="now-m-inline-start--xxs"
					/>
				</slot>
			</now-button>
			<div
				ref={popoverRef}
				target-ref={popoverTargetRef}
				class={{
					'popover-content': true,
					'now-p-block--sm': true,
					'now-p-inline--sm': true,
					'popover-show': showPopover
				}}>
				<slot name="popover-content" />
				{/**
					copied from eds-modal
					This button is in here as a workaround for an obscure browser
	    		bug that prevents us from calling `evt.preventDefault()` in response
	    		to a tab press originating from the last focusable element on the
	    		page. In order to trap focus in the popover in the case where the
					popover contains the last focusable element on the page, we add a
					focusable, but invisible "decoy" element to the end of the popover.
					This allows us to trap focus within the popover without ever actually
					focusing on the last focusable element on the page, thus avoiding
					the bug. Bof.
				*/}
				<button aria-hidden="true" className="now-a11y-label" />
			</div>
		</div>
	);
};

/**
 * Button that toggles small overlays of content for housing secondary information.
 *
 * ```jsx
 * <timeline-popover-button
 * 	 showLabel="Show popover"
 * 	 hideLabel="Hide popover"
 * 	 <div slot="popover-content">
 * 	 </div>
 * </timeline-popover-button>
 * ```
 *
 * @seismicElement timeline-popover
 * @summary Button that toggles small overlays of content for housing secondary information.
 */
createCustomElement('timeline-popover-button', {
	styles,
	renderer: {
		view: view
	},
	properties: {
		disableLegend: {
			default: false
		},
		/**
		 * The title used for events in the legend.
		 * @type {string}
		 */
		eventTitle: {
			default: t('Timeline indicators')
		},
		/**
		 * Object array that defines the events to be displayed on the legend.
		 * Each object must have a unique name.
		 * The icon must be taken from the gallery of available eds icons.
		 * The color used for the icon must be a full hex string.
		 * The data object can be any valid json specific to the particular event.
		 *
		 * The events objects are configured with the following keys:
		 * - `timestamp: number` required - UTC timestamp for the event
		 * - `description: string` required - Short text description for the event
		 * - `data: object` - Any valid json object that can be associated with the particular event
		 * @type {{name: string, icon: string, color: string, label: string, labelPlural: string, events: Object[]}[]}
		 */
		eventData: {
			default: [],
			cast: 'array'
		},
		/**
		 * The title used for ranges in the legend.
		 * @type {string}
		 */
		rangeTitle: {
			default: t('Timeline ranges')
		},
		/**
		 * Object array that defines the ranges to be displayed on the legend.
		 * Each object must have a unique name.
		 * The color used for the range must be a full hex string.
		 * The data object can be any valid json specific to the particular range.
		 *
		 * The ranges objects are configured with the following keys:
		 * - `start: number` required - Beginning date for the range as a UTC timestamp
		 * - `end: number` required - Ending date for the range as a UTC timestamp
		 * - `description: string` required - Short text description for the range
		 * - `data: object` - Any valid json object that can be associated with the particular range
		 * @type {{name: string, color: string, label: string, ranges: Object[]}[]}
		 */
		rangeData: {
			default: [],
			cast: 'array'
		},
		/**
		 * Label used when popover is hidden.
		 * @type {string}
		 */
		showLabel: {
			default: t('Show')
		},
		/**
		 * Label used when popover is displayed.
		 * @type {string}
		 */
		hideLabel: {
			default: t('Hide')
		},
		/**
		 * An object whose items, all aria properties, will be set on the inner
		 * html `<button>`.
		 * See https://www.w3.org/TR/wai-aria-1.1/#button for properties and
		 * accepted values.
		 * @type {{ 'aria-*': string }}
		 */
		configAria: {cast: 'object'},
		/** @private */
		showPopover: {
			default: false
		},
		/** @private */
		fullAriaConfig: {
			computed({
				properties: {
					configAria,
					showPopover,
					eventTitle,
					eventData,
					rangeTitle,
					rangeData,
					showLabel,
					hideLabel
				}
			}) {
				let itemLabel = [];
				if (eventData && eventData.length > 0) {
					itemLabel.push(t('{0}', eventTitle));
					map(eventData, (item) =>
						itemLabel.push(t('{0},{1}', item.label, item.events.length))
					);
					itemLabel.push(t(hideLabel));
				}
				if (rangeData && rangeData.length > 0) {
					itemLabel.push(t('{0}', rangeTitle));
					map(rangeData, (item) =>
						itemLabel.push(t('{0},{1}', item.label, getRangeDisplay(item)))
					);
					itemLabel.push(t(hideLabel));
				}
				let ariaLabel = !showPopover
					? t(showLabel)
					: t('{0}', itemLabel.toString());
				return {
					...configAria,
					'aria-pressed': showPopover.toString(),
					'aria-label': ariaLabel
				};
			}
		}
	},
	slots: {
		/**
		 * Content placed in this named slot will appear when the popover is displayed.
		 * Click behavior and other functionality will be maintained for the custom content automatically.
		 */
		'popover-content': {}
	},
	actionHandlers: {
		'NOW_BUTTON#CLICKED': {
			private: true,
			stopPropagation: true,
			effect: ({action, state, dispatch}) => {
				const {showPopover} = state.properties;
				const {buttonRef, popoverRef} = action.payload;

				// toggle the popover on button click and attach outside element click listener
				// only want to toggle the popover if it's not already shown
				if (showPopover) {
					return;
				}

				// show the popover and wire up event listeners
				dispatch(TIMELINE_SHOW_POPOVER);

				// keep the button in focus in order for focus trap to work on popover elements
				buttonRef.current.focus();

				// delay here to give popover a chance to be displayed
				setTimeout(() => {
					hideOnClickOutside(buttonRef.current, popoverRef.current, dispatch);
				}, 200);
			}
		},
		TIMELINE_SHOW_POPOVER: {
			private: true,
			stopPropagation: true,
			effect: ({updateProperties}) => {
				// reset the last focused element for Safari
				lastFocusedElementLocal = null;
				updateProperties({showPopover: true});
			}
		},
		TIMELINE_HIDE_POPOVER: {
			private: true,
			stopPropagation: true,
			effect: ({updateProperties}) => {
				// reset the last focused element for Safari
				lastFocusedElementLocal = null;
				updateProperties({showPopover: false});
			}
		},
		TIMELINE_POPOVER_ESC_TAB_PRESS: {
			private: true,
			effect: ({action, state, dispatch}) => {
				const {host, evt, isTab} = action.payload;
				const {showPopover} = state.properties;

				if (isTab) {
					handleTabPress(host, evt, showPopover);
					return;
				}

				handleEscapePress(host, evt, dispatch, showPopover);
			}
		}
	},
	behaviors: [
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					onTabPress: ['Tab'],
					onEscapePress: ['Escape']
				},
				handlers: {
					onTabPress: (host, evt, dispatch) => {
						// dispatch to handler for access to state properties
						dispatch(TIMELINE_POPOVER_ESC_TAB_PRESS, {
							host: host,
							evt: evt,
							isTab: true
						});
					},
					onEscapePress: (host, evt, dispatch) => {
						// dispatch to handler for access to state properties
						dispatch(TIMELINE_POPOVER_ESC_TAB_PRESS, {
							host: host,
							evt: evt,
							isTab: false
						});
					}
				}
			}
		}
	]
});
