let tooltipTimer;
const renderMap = new WeakMap();

const createSvgElement = (child) => {
	// make sure there is a child
	if (!child) {
		return child;
	}

	// create the NS element
	let nsElement = document.createElementNS(
		'http://www.w3.org/2000/svg',
		child.nodeName.toLowerCase()
	);
	const attrs = child.attributes;

	// add in the hasAttributes
	for (let i = attrs.length - 1; i >= 0; i--) {
		nsElement.setAttribute(attrs[i].name, attrs[i].value);
	}

	// add in the children if any
	let childNode = child.firstChild;

	while (childNode) {
		nsElement.appendChild(createSvgElement(childNode));
		childNode = childNode.nextSibling;
	}

	return nsElement;
};

const getChartIconSvg = (iconName, iconMap) => {
	// existing icon?
	const existingIcon = iconMap[iconName];

	if (existingIcon) {
		// make sure to return clone of the element
		return existingIcon.cloneNode(true);
	}

	let iconElement = document.createElement('SVG', null);

	// handle different versions on eds-ui-icon
	iconElement.innerHTML = icons[iconName].html
		? icons[iconName].html
		: icons[iconName];

	// assuming that icons only have one 'g' child
	// Note: this is a hack to get icons to render correctly in IE 11
	const iconSvgGroup = createSvgElement(iconElement.firstChild);

	// Note: if the 'g' element already has a class it will be overriden
	iconSvgGroup.setAttribute('class', 'timeline-chart-icon');

	// add it to dictionary for lookup
	iconMap[iconName] = iconSvgGroup.cloneNode(true);
	return iconSvgGroup;
};

const renderEvents = (
	eventData,
	config,
	canvas,
	xScale,
	svgRef,
	tooltipRef,
	tooltipId,
	state,
	props,
	dispatch,
	updateState
) => {
	if (eventData.length === 0) {
		return;
	}

	const events = [];

	// Build events array by combining eventData
	for (const key in eventData) {
		for (const event in eventData[key].events) {
			const eventObj = {...eventData[key].events[event]};
			const typeObj = {...eventData[key]};
			typeObj.hover = false;
			typeObj.selected = false;
			typeObj.icon = `${typeObj.icon}16`;
			typeObj.type = 'event';
			delete typeObj.events;
			events.push({...eventObj, ...typeObj});
		}
	}

	// Sort events by timestamp
	events.sort((a, b) => {
		return a.timestamp - b.timestamp;
	});

	const {selectedEvents, manageSelections} = props;
	if (manageSelections) {
		applySelectedItems(events, selectedEvents);
	}

	// save off the ranges in locals
	updateState({events: events});

	// stripEvents
	canvas
		.selectAll('svg.strip-event')
		.data(events)
		.enter()
		.append('svg')
		.attr('x', function(d) {
			d.eventIndicator = this;
			return xScale(d.timestamp) - 5;
		})
		.attr('y', config.eventsY)
		.attr('class', 'strip-event')
		.attr('role', 'button')
		.attr('aria-labelledby', tooltipId)
		.on('focus', function(d) {
			dispatch(TIMELINE_HOVER_CHART_ELEMENT, {data: d, show: true});
			dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
		})
		.on('blur', function(d) {
			d3.select(this).classed('highlighted', false);
			dispatch(TIMELINE_HOVER_CHART_ELEMENT, {data: d, show: false});
		})
		.on('click', (evt) => {
			// don't bubble up to hotspot click
			d3.event.stopPropagation();
			dispatch(TIMELINE_CHART_EVENT_SELECT, {evt: evt, d3Evt: d3.event});
			dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
		})
		.on('keypress', (evt) => {
			if (d3.event.keyCode == 13 || d3.event.keyCode == 32) {
				dispatch(TIMELINE_CHART_EVENT_SELECT, {evt: evt, d3Evt: d3.event});
				dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
				d3.event.preventDefault();
			}
		})
		.append('rect');

	// eventIcons
	canvas
		.selectAll('svg.event-icon')
		.data(events)
		.enter()
		.append('svg')
		.attr('x', (d) => {
			return xScale(d.timestamp) - 3;
		})
		.attr('y', (d) => {
			return d.icon === 'group' ? config.iconsY - 4 : config.iconsY;
		})
		.attr('class', 'event-icon');

	// event hotspot to give a bigger click area for icons
	canvas
		.selectAll('rect.timeline-event-hotspot')
		.data(events)
		.enter()
		.append('rect')
		.attr('class', 'timeline-event-hotspot')
		.attr('x', (d) => {
			return xScale(d.timestamp);
		})
		.attr('y', config.iconsY - 2)
		.attr('height', '38')
		.attr('role', 'button')
		.attr('aria-labelledby', tooltipId)
		.on('mouseenter', function(d) {
			dispatch(TIMELINE_HOVER_CHART_ELEMENT, {data: d, show: true});
		})
		.on('mousemove', function() {
			// reset timer when user moves mouse over element. prevents tooltip from showing until user stops moving mouse over element for 300ms
			dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
		})
		.on('mouseout', function(d) {
			d3.select(this).classed('highlighted', false);
			dispatch(TIMELINE_HOVER_CHART_ELEMENT, {data: d, show: false});
		})
		.on('click', (evt) => {
			if (evt.eventIndicator.focus) {
				evt.eventIndicator.focus();
			} else {
				// IE 11 hack to focus on svg element
				// https://allyjs.io/tutorials/focusing-in-svg.html
				HTMLElement.prototype.focus.apply(evt.eventIndicator);
			}

			dispatch(TIMELINE_CHART_EVENT_SELECT, {evt: evt, d3Evt: d3.event});
			dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
		});
};

const renderRanges = (
	rangeData,
	config,
	canvas,
	xScale,
	svgRef,
	tooltipRef,
	tooltipId,
	state,
	props,
	dispatch,
	updateState
) => {
	if (rangeData.length === 0) {
		return;
	}

	const ranges = [];

	// Build ranges array by combining rangeData
	// Flatten all ranges into a single list with icon, color, label and name populated to each
	for (const key in rangeData) {
		for (const range in rangeData[key].ranges) {
			const rangeObj = {...rangeData[key].ranges[range]};
			const typeObj = {...rangeData[key]};
			typeObj.type = 'range';
			typeObj.hover = false;
			typeObj.selected = false;
			delete typeObj.ranges;
			ranges.push({...rangeObj, ...typeObj});
		}
	}

	// Sort range by start
	ranges.sort((a, b) => {
		return a.start - b.start;
	});

	const {selectedRanges, manageSelections} = props;
	if (manageSelections) {
		applySelectedItems(ranges, selectedRanges);
	}

	// save off the ranges in state
	updateState({ranges: ranges});

	// Ranges
	canvas
		.selectAll('rect.timeline-ranges')
		.data(ranges)
		.enter()
		.append('rect')
		.attr('x', (d) => {
			return xScale(d.start);
		})
		.attr('y', config.rangeY)
		.attr('width', (d) => {
			return xScale(d.end) - xScale(d.start);
		})
		.attr('height', 12)
		.attr('class', 'timeline-ranges')
		.attr('role', 'button')
		.attr('aria-labelledby', tooltipId)
		.style('fill', (d) => {
			return d.color;
		})
		.on('mouseenter', function(d) {
			let transition = d3
				.select(this)
				.transition()
				.duration(100);
			transition.attr('y', config.rangeY - 2);
			transition.attr('height', 16);
			dispatch(TIMELINE_HOVER_CHART_ELEMENT, {data: d, show: true});
		})
		.on('mousemove', function() {
			// reset timer when user moves mouse over element. prevents tooltip from showing until user stops moving mouse over element for 300ms
			dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
		})
		.on('mouseleave', function(d) {
			let transition = d3
				.select(this)
				.transition()
				.duration(100);
			transition.attr('y', config.rangeY);
			transition.attr('height', 12);
			dispatch(TIMELINE_HOVER_CHART_ELEMENT, {data: d, show: false});
		})
		.on('click', (evt) => {
			dispatch(TIMELINE_CHART_RANGE_SELECT, {evt: evt, d3Evt: d3.event});
			dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
		})
		.on('keypress', (evt) => {
			if (d3.event.keyCode == 13 || d3.event.keyCode == 32) {
				dispatch(TIMELINE_CHART_RANGE_SELECT, {evt: evt, d3Evt: d3.event});
				d3.event.preventDefault();
			}
		})
		.on('focus', function(d) {
			let transition = d3
				.select(this)
				.transition()
				.duration(100);
			transition.attr('y', config.rangeY - 2);
			transition.attr('height', 16);
			dispatch(TIMELINE_HOVER_CHART_ELEMENT, {data: d, show: true});
			dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
		})
		.on('blur', function(d) {
			let transition = d3
				.select(this)
				.transition()
				.duration(100);
			transition.attr('y', config.rangeY);
			transition.attr('height', 12);
			dispatch(TIMELINE_HOVER_CHART_ELEMENT, {data: d, show: false});
		})
		.on('contextmenu', function() {
			// prevent contextmenu if control or command key is pressed
			if (d3.event.metaKey || d3.event.ctrlKey) {
				d3.event.preventDefault();
			}
		});

	// Range selectors
	canvas
		.selectAll('rect.rangeselectors')
		.data(ranges)
		.enter()
		.append('rect')
		.attr('x', (d) => {
			return xScale(d.start);
		})
		.attr('y', config.rangeY + 14)
		.attr('width', (d) => {
			return xScale(d.end) - xScale(d.start);
		})
		.attr('height', 2)
		.attr('rx', 2) // set the x corner curve radius
		.attr('ry', 2) // set the y corner curve radius
		.attr('class', 'rangeselectors strip-range-selected');
};

const getTimestamp = (date) => (date && date.getTime ? date.getTime() : -1);

const timestampsSimilar = (t1, t2) => Math.abs(t1 - t2) < 50;

/**
 * Debounced function to set initialized=true state and dispatch TIMELINE_CHART_INITIALIZED.
 * The debounce timeout should be larger than the longest d3 transition duration (longest transition is 300 last time I checked).
 */
const setInitialized = debounce(({dispatch, config, props}) => {
	const currentStartTimestamp = getTimestamp(config.currentViewStart);
	const currentEndTimestamp = getTimestamp(config.currentViewEnd);

	if (currentStartTimestamp === -1 || currentEndTimestamp === -1) {
		return;
	}

	// keep track of the last values that were dispatched
	config.lastDispatchedStart = currentStartTimestamp;
	config.lastDispatchedEnd = currentEndTimestamp;

	// the timeline is now initialized, reseting hover state
	config.initialized = true;
	config.isHoverEvent = false;

	dispatch(TIMELINE_CHART_INITIALIZED, {
		currentViewStart: new Date(currentStartTimestamp),
		currentViewEnd: new Date(currentEndTimestamp)
	});

	//becasue of the debounce, we may skip first user ineration after initialize.
	//this condition detects that latest update of D3 zoom differs from the input props and should be disaptched as TIMELINE_CHART_RANGE_CHANGE
	if (
		!timestampsSimilar(props.visibleStart, currentStartTimestamp) ||
		!timestampsSimilar(props.visibleEnd, currentEndTimestamp)
	) {
		dispatch(TIMELINE_CHART_RANGE_CHANGE, {
			currentViewStart: currentStartTimestamp,
			currentViewEnd: currentEndTimestamp
		});
	}
}, 500);

/**
 * Effect that is called on D3 zoom.end
 */
const onZoomEnd = ({state, dispatch, properties: props, action}) => {
	const {config} = state;

	const callbackFn = get(action, 'payload.zoomEvent.transform.callbackFn');

	if (callbackFn) {
		callbackFn();
	}

	dispatch(TIMELINE_CHART_PAN_ZOOM_TOGGLE, {
		zoomIn: config.allowZoomIn,
		zoomOut: config.allowZoomOut,
		panPrev: config.allowpanPrev,
		panNext: config.allowpanNext
	});

	const currentStartTimestamp = getTimestamp(config.currentViewStart);
	const currentEndTimestamp = getTimestamp(config.currentViewEnd);

	// no values have been set yet for dates so no need to dispatch any events
	if (currentStartTimestamp === -1 || currentEndTimestamp === -1) {
		return;
	}

	// checking here to determine if this is the first complete rendering of the timeline
	if (!config.initialized) {
		//debounce initialization to skip unneccesary range updates.
		setInitialized({
			props,
			dispatch,
			currentStartTimestamp,
			currentEndTimestamp,
			config
		});

		return;
	}

	//DEF0107151: not skipping timeline range change events on hover, to allow the tooltip to show up on hover.

	//detect when current range is updated by D3 and is different from last dispatched range
	if (
		currentStartTimestamp !== config.lastDispatchedStart ||
		currentEndTimestamp !== config.lastDispatchedEnd
	) {
		// keep track of the last values that were dispatched
		config.lastDispatchedStart = currentStartTimestamp;
		config.lastDispatchedEnd = currentEndTimestamp;

		dispatch(TIMELINE_CHART_RANGE_CHANGE, {
			currentViewStart: currentStartTimestamp,
			currentViewEnd: currentEndTimestamp
		});
	}
};

const setupZoom = (
	config,
	tooltipRef,
	svg,
	gX,
	xAxis,
	xScale,
	props,
	dispatch,
	updateState
) => {
	// remove all the current handlers
	svg
		.on('.zoom', null)
		.on('start', null)
		.on('end', null);

	// pass these values to keep zoom and data local to only this timeline
	const zoom = d3
		.zoom()
		.scaleExtent([1, config.maxZoom])
		.translateExtent([
			[xScale(new Date(config.minDate)), 10000],
			[xScale(new Date(config.maxDate)), 10000]
		])
		.on('start', null) //palceholder, can put the callback function here
		.on('zoom', () => {
			dispatch(TIMELINE_RUN_CHART_ZOOM, {zoomEvent: d3.event});
		})
		.on('end', () => {
			dispatch(TIMELINE_ON_ZOOM_END, {zoomEvent: d3.event});
		});

	// Initial transform to apply
	// https://stackoverflow.com/questions/16178366/d3-js-set-initial-zoom-level
	const transform = d3.zoomIdentity.translate(0, 0).scale(1);

	// save off objects for use in other dispatched events
	updateState({
		zoomObj: zoom,
		gX: gX,
		xAxis: xAxis,
		xScale: xScale,
		config: config
	});

	transform.callbackFn = () => {
		if (!props.allowZoomPan) {
			// fit svg to the container
			dispatch(TIMELINE_DO_CHART_RESIZE);

			// make sure to remove zoom listener if zoom not allowed through properties
			svg
				.on('.zoom', null)
				.on('start', null)
				.on('end', null);
		} else {
			// Zoom to visible start and end
			const displayScale = d3.zoomIdentity
				.scale(
					config.width / (xScale(props.visibleEnd) - xScale(props.visibleStart))
				)
				.translate(-xScale(props.visibleStart), 0);
			displayScale.callbackFn = () => {
				// Just like zoom, need to resize here so the svg is redrawn to fit container
				dispatch(TIMELINE_DO_CHART_RESIZE);
			};

			svg
				.transition()
				.duration(0)
				.call(zoom.transform, displayScale);
		}
	};

	// wires up zoom event listener and initializes zoom
	svg.call(zoom).call(zoom.transform, transform);
};

const setupResizeListener = (dispatch, updateState) => {
	const hostToResizeListener = new WeakMap();
	const addResizeListener = (host, dispatch) => {
		const listener = () => {
			dispatch(TIMELINE_DO_CHART_RESIZE);
		};

		window.addEventListener('resize', listener, false);
		hostToResizeListener.set(host, listener);
	};
	const removeResizeListener = (host) => {
		const listener = hostToResizeListener.get(host);

		if (listener) {
			window.removeEventListener('resize', listener, false);
			hostToResizeListener.delete(host);
		}
	};

	updateState({
		addResizeListener: addResizeListener,
		removeResizeListener: removeResizeListener
	});

	dispatch(TIMELINE_CHART_INIT_RESIZE_LISTENER);
};

const handleChartUpdate = ({
	svgRef,
	tooltipRef,
	tooltipId,
	state,
	properties,
	dispatch,
	updateState
}) => {
	const svg = d3.select(svgRef.current);
	const svgWidth = parseInt(svg.attr('width'));
	const svgHeight = parseInt(svg.attr('height'));
	const props = properties;

	// clear the current svg
	svg.selectAll('*').remove();

	// create the shadow filter
	// https://software.intel.com/en-us/blogs/2013/09/12/svg-filters-putting-pieces-together
	const defs = svg.append('defs');

	const filter = defs
		.append('filter')
		.attr('id', 'drop-shadow-timeline')
		.attr('filterUnits', 'userSpaceOnUse')
		.attr('x', -1) // removes border around base svg that displays when filter is applied
		.attr('y', -1) // removes border around base svg that displays when filter is applied
		.attr('height', '110%')
		.attr('width', '110%');

	filter
		.append('feGaussianBlur')
		.attr('in', 'SourceAlpha')
		.attr('stdDeviation', 4)
		.attr('result', 'blur');

	filter
		.append('feOffset')
		.attr('in', 'blur')
		.attr('dx', 2)
		.attr('dy', 2)
		.attr('result', 'offsetBlur');

	filter
		.append('feComposite')
		.attr('in', 'SourceGraphic')
		.attr('in2', 'offsetBlur')
		.attr('operator', 'over');

	const canvas = svg
		.append('g')
		.attr('class', 'timeline-canvas')
		.attr('width', svgWidth)
		.attr('height', svgHeight)
		.attr('transform', 'translate(0,0)');

	// start with deep copy of event and range data
	const eventData = JSON.parse(JSON.stringify(props.eventData));
	const rangeData = JSON.parse(JSON.stringify(props.rangeData));

	const config = {
		minDate: props.minDate,
		maxDate: props.maxDate,
		maxZoom: 16,
		width: svgWidth,
		height: svgHeight,
		stripY: svgHeight - 31,
		stripHeight: 16,
		eventsY: svgHeight - 43,
		iconsY: svgHeight - 60,
		rangeY: svgHeight - 30,
		initialized: false
	};

	// have range data and move event indicators above strip
	if (rangeData.length > 0) {
		config.stripHeight = 12;
		config.eventsY = svgHeight - 38;
		config.iconsY = svgHeight - 55;
	}

	const xScale = d3
		.scaleTime()
		.domain([props.minDate, props.maxDate])
		.range([0, svgWidth]);
	const xAxis = d3
		.axisBottom(xScale)
		.tickPadding(1)
		.tickSize(4);

	// calculate max zoom in
	config.maxZoom =
		svgWidth /
		(xScale(moment(props.minDate).add(props.maxZoomMilli, 'milliseconds')) -
			xScale(props.minDate));

	// clip
	canvas
		.append('clipPath')
		.attr('id', 'clip')
		.append('rect')
		.attr('width', svgWidth)
		.attr('height', svgHeight);

	const gX = canvas
		.append('g')
		.attr('transform', 'translate(0,' + (config.rangeY + 14) + ')')
		.attr('class', 'axis axis--x timeline-x-axis')
		.call(xAxis);

	// strip
	canvas
		.append('rect')
		.attr('x', 0)
		.attr('y', svgHeight - config.stripHeight - 18)
		.attr('width', svgWidth)
		.attr('height', config.stripHeight)
		.attr('rx', 2) // set the x corner curve radius
		.attr('ry', 2) // set the y corner curve radius
		.attr('class', 'timeline-strip');

	// having issues with mouse exit on events and ranges not firing when performing mouse wheel zoom randomly.
	// this adds an extra layeer of protection to make sure tooltip is hidden.
	svg.on('mouseenter', function() {
		dispatch(TIMELINE_DELETE_CHART_TOOLTIP_TIMER);
	});

	// add the current data to the svg display
	renderEvents(
		eventData,
		config,
		canvas,
		xScale,
		svgRef,
		tooltipRef,
		tooltipId,
		state,
		props,
		dispatch,
		updateState
	);
	renderRanges(
		rangeData,
		config,
		canvas,
		xScale,
		svgRef,
		tooltipRef,
		tooltipId,
		state,
		props,
		dispatch,
		updateState
	);

	// attach zoom event handlers and zoom to initial scale
	setupZoom(
		config,
		tooltipRef,
		svg,
		gX,
		xAxis,
		xScale,
		props,
		dispatch,
		updateState
	);
	setupResizeListener(dispatch, updateState);

	const transform = JSON.parse(
		JSON.stringify(d3.zoomTransform(svg.node()))
	);
	const width = svg
		.select('rect.timeline-strip')
		.node()
		.getBBox().width;
	const step = (width * (state.properties.panStepPercent / 100)) / transform.k;
	const panDuration = (state.properties.maxDate - state.properties.minDate) * step / width;
	panDurationUpdate(panDuration, dispatch);
};

const handleChartInsert = (svgRef, timelineRef) => {
	const svg = d3.select(svgRef.current);
	svg.attr('width', 0);
	const svgWidth =
		timelineRef.current.clientWidth > 0 ? timelineRef.current.clientWidth : 1;
	const svgHeight = 70;

	svg.attr('width', svgWidth);
	svg.attr('height', svgHeight);
	svg.attr('class', 'timeline-chart-svg');
};

const setTooltipTimer = (tooltipRef) => {
	if (tooltipTimer) {
		clearTimeout(tooltipTimer);
	}

	tooltipTimer = setTimeout(() => {
		d3.select(tooltipRef.current)
			.classed('hidden', false)
			.transition()
			.duration(200)
			.style('opacity', 0.9);
	}, 300);
};

/** @seismicView timeline-chart */
const view = (state, {dispatch, updateState}) => {
	const {properties} = state;
	const svgRef = createRef();
	const tooltipRef = createRef();
	const timelineRef = createRef();
	const {rtl} = properties;
	const {host} = state;

	// TODO: MAKE SURE UNIQUE ID IS GETTING SET FOR A11Y
	const componentId =
		host && typeof host.getAttribute === 'function'
			? host.getAttribute('component-id')
			: '';
	const tooltipId = `${componentId}_timeline_tooltip`;

	const verifyChartUpdate = () => {
		const propsNow = JSON.parse(JSON.stringify(properties));
		const propsThen = state.propsObj;

		// re-render?
		// this is a bit hacky because of the way hooks work with locals, properties and onChange events of properties.
		// everything seems to fire the update hook whenever anything is changed
		if (isEqual(propsNow, propsThen)) {
			return;
		}

		let timerFn = renderMap.get(host);

		if (timerFn) {
			clearTimeout(timerFn);
		}

		// hack here to delay rendering multiple times on property changes
		timerFn = setTimeout(() => {
			updateState({
				svgRef,
				timelineRef,
				tooltipRef,
				propsObj: propsNow
			});
			handleChartUpdate({
				svgRef,
				tooltipRef,
				tooltipId,
				state,
				properties,
				dispatch,
				updateState
			});
		}, 0);

		if (host) {
			renderMap.set(host, timerFn);
		}
	};

	// this is the actual view
	return (
		<div className="timeline-chart-layout" ref={timelineRef}>
			<svg
				ref={svgRef}
				hook-update={() => {
					handleChartInsert(svgRef, timelineRef);
					verifyChartUpdate();
				}}
			/>
			<div
				dir={rtl ? 'rtl' : 'ltr'}
				ref={tooltipRef}
				className="timeline-tooltip hidden"
				id={tooltipId}
			/>
		</div>
	);
};

export const getUserLanguage = (user) => {
	const fallback = get(
		navigator,
		'languages[0]',
		navigator.language || navigator.userLanguage
	);

	return user.language || fallback;
};

function getCurrentUser() {
	let currentUser = _.get(
		window,
		'ux_globals.presource["sn-workspace-header:wsUserData"].data.GlideDomain_Query.user',
		{}
	);
	return currentUser;
};

const setTooltip = ({svgRef, tooltipRef, data, isCompactView}) => {
	const tooltip = d3.select(tooltipRef.current);
	const singleFormat = 'lll'; // localized version of date
	const multiFormat = 'll'; // localized version of date
	const language = getUserLanguage(getCurrentUser());
	// hide tooltip and set width to auto so we can get the correct width once content is inserted.
	tooltip
		.style('left', null)
		.style('bottom', null)
		.style('width', 'auto');

	let html = '';
	let startMoment, endMoment;

	if (data.type === 'event') {
		startMoment = moment(data.timestamp).locale(language).format(multiFormat);
		endMoment = moment(data.endTimestamp).locale(language).format(multiFormat);
		if (data.eventCount < 1) {
			// Single Event
			html = `<div>${data.description}<br />${t(
				'{0}',moment(data.timestamp).locale(language).format(singleFormat)
			)}</div>`;
		} else {
			if (!data.eventIcon) {
				// Group Event with Icon
				let items = [];
				// Get the count for each event type in the group.
				// Add the first item here as it is not part of the groupEvents.
				items[data.name] = {
					label: data.label,
					labelPlural: data.labelPlural,
					count: 1
				};

				data.groupEvents.forEach((event) => {
					if (!items[event.name]) {
						items[event.name] = {
							label: event.label,
							labelPlural: event.labelPlural,
							count: 0
						};
					}

					items[event.name].count++;
				});

				for (const key in items) {
					const itemLabel =
						items[key].count > 1 ? items[key].labelPlural : items[key].label;
					html += `<div>${items[key].count} ${itemLabel}</div>`;
				}

				if (startMoment != endMoment) {
					html += t('between {0} and {1}', startMoment, endMoment);
				} else {
					html += t('on {0}', startMoment);
				}
			} else {
				// Group Event without Icon
				html = `<div>${t('Lots of events')}</div>`;
				if (startMoment != endMoment) {
					html += t('between {0} and {1}', startMoment, endMoment);
				} else {
					html += t('on {0}', startMoment);
				}
			}
		}
	}

	if (data.type === 'range') {
		startMoment = moment(data.start)
			.locale(language)
			.format(multiFormat);
		endMoment = moment(data.end)
			.locale(language)
			.format(multiFormat);
		if (startMoment != endMoment) {
			html += t('{0} between {1} and {2}', data.label, startMoment, endMoment);
		} else {
			html += t('{0} on {1}', data.label, startMoment);
		}
		html = `<div>${html}</div>`;
	}

	// set the content of the tooltip so width can be calculated
	tooltip.classed('hidden', false).html(html);

	const tooltipBounds = tooltipRef.current.getBoundingClientRect(); // DOMRect
	const svgBounds = svgRef.current.getBoundingClientRect(); // DOMRect
	const targetX = data.x + data.width / 2;
	const tooltipWidth = tooltipBounds.width;
	let tooltipX = targetX - tooltipWidth / 2;
	let tooltipBottom = 52;

	if (data.type === 'event' && data.icon) {
		tooltipBottom = isCompactView ? -10 : 75;
	}

	if (data.type === 'range') {
		tooltipBottom = 42;
	}

	// make sure the tooltip stays within the right side bounds.
	if (tooltipX + tooltipBounds.width > svgBounds.width) {
		tooltipX = svgBounds.width - tooltipBounds.width - 2;
	}

	// make sure the tooltip stays within the left side bounds. Do this second to insure the left side of the tooltip is always displayed.
	if (tooltipX < 5) {
		tooltipX = 5;
	}

	tooltip
		.style('left', tooltipX + 'px')
		.style('bottom', tooltipBottom + 'px')
		.style('width', tooltipWidth + 'px'); // Need to set width to avoid div from wrapping when its on the edge of the screen.
	tooltip.classed('hidden', true);
};
const hideTooltip = (tooltipRef) => {
	if (tooltipTimer) {
		clearTimeout(tooltipTimer);
	}
	d3.select(tooltipRef.current).classed('hidden', true);
};

const zoomChart = ({
	zoomEvent,
	svg,
	gX,
	xAxis,
	xScale,
	config,
	state,
	properties
}) => {
	zoomEvent = zoomEvent || d3.event;

	// TODO: NEED TO FIGURE OUT WHY d3.event is null sometimes
	if (!zoomEvent) {
		return;
	}

	// get width of SVG
	const chartWidth = svg.attr('width');

	// adjust ticks size to chart width. max is 10. min is 2
	let ticks = Math.round(chartWidth / 100);
	if (ticks > 10) {
		ticks = 10;
	}
	if (ticks < 2) {
		ticks = 2;
	}
	xAxis.ticks(ticks);

	// Update Time Line
	gX.call(xAxis.scale(zoomEvent.transform.rescaleX(xScale)));

	// Create new X scale with new zoom settings
	const newXScale = zoomEvent.transform.rescaleX(xScale);
	const {events, ranges} = state;

	// Set zoom/pan status
	config.allowZoomIn = zoomEvent.transform.k < config.maxZoom ? true : false;
	config.allowZoomOut = zoomEvent.transform.k > 1 ? true : false;
	config.allowpanPrev = zoomEvent.transform.x < 0 ? true : false;
	config.allowpanNext =
		xScale(new Date(config.maxDate)) < newXScale(new Date(config.maxDate))
			? true
			: false;

	// Set groupId to none
	let groupId = -1;
	let lastId = 0;
	let count = 0;

	// Loop through events checking for touching. Using for loop for performace.
	for (let i = 0; i < events.length; i++) {
		// Set x to scaled value
		events[i].x = newXScale(events[i].timestamp) - 3; // Center Icons

		// Reset Default value for event data
		events[i].visibility = 'visible';
		events[i].width = 8; // 8 is the default value
		events[i].group = false;
		events[i].eventCount = 0;
		events[i].eventSelectedCount = events[i].selected ? 1 : 0;
		events[i].iconName = events[i].icon ? events[i].icon : null;
		events[i].iconColor = events[i].color ? events[i].color : '#506163'; // neutral-10
		events[i].groupEvents = [];
		// Only check for touching after first event.
		if (!events[i].hidden) {
			if (count > 0) {
				const gapTest = 8;
				let testId = lastId;
				if (groupId != -1) {
					testId = groupId;
				}
				if (events[i].x < events[testId].x + events[testId].width + gapTest) {
					// +7 is the buffer between the event icons.
					// Touching
					events[i].visibility = 'hidden';
					events[testId].width =
						events[i].x - events[testId].x + events[i].width;
					events[testId].group = true;
					events[testId].iconName = 'group';
					events[testId].iconColor = '#506163'; // neutral-10
					events[testId].eventCount++; // Increase total events for this group
					events[testId].endTimestamp = events[i].timestamp; // Set group endTimeStamp to current event timestamp
					events[testId].groupEvents.push(events[i]);
					if (events[i].selected) {
						events[testId].eventSelectedCount++;
					}
					if (groupId === -1) {
						groupId = testId;
					}
				} else {
					groupId = -1; // No current active group
				}
			}
			// Should include all the events that are visible on the timeline.
			lastId = i;
			count++;
		} else {
			events[i].visibility = 'hidden';
		}
	}

	const {highlightedEventIds} = properties;
	const highlightIndex = new Set(highlightedEventIds || []);

	//set highlighted events directly
	events.forEach((e) => (e.highlighted = highlightIndex.has(getItemId(e))));

	//resolve groups that have highlighted events
	events
		.filter((e) => !e.highlighted && e.groupEvents.some(isHighlighted))
		.forEach((e) => (e.highlighted = true));

	// Loop through ranges setting x and width. Using for loop for performace.
	for (let i = 0; i < ranges.length; i++) {
		// Set x to scalled value
		ranges[i].x = newXScale(ranges[i].start);
		ranges[i].width = newXScale(ranges[i].end) - newXScale(ranges[i].start);
	}

	// Move Events Time Strip Icon
	const canvas = svg.select('g.timeline-canvas');

	canvas
		.selectAll('svg.strip-event')
		.selectAll('*')
		.remove();

	canvas
		.selectAll('svg.strip-event')
		.data(events)
		.attr('x', (d) => {
			return d.x + 2;
		})
		.attr('y', config.eventsY)
		.style('visibility', (d) => {
			return d.visibility;
		})
		.attr('tabindex', function(d) {
			if (d.x + d.width > 2 && d.x < chartWidth - 2) {
				return 0;
			}
			return -1;
		})
		.attr('focusable', function(d) {
			// required for tab key to work in IE11 and Edge
			if (d.x + d.width > 2 && d.x < chartWidth - 2) {
				return true;
			}
			return false;
		})
		.append((d) => {
			var ns = 'http://www.w3.org/2000/svg';
			let stripEventClass = '';
			if (d.hover) {
				stripEventClass = 'hover';
			} else if (d.selected) {
				stripEventClass = 'selected';
			} else if (d.highlighted) {
				stripEventClass = 'highlighted';
			}
			if (d.iconName !== 'group') {
				stripEventClass = stripEventClass += ' strip-event';
				var rectElement = document.createElementNS(ns, 'rect');
				rectElement.setAttribute('visibility', d.visibility);
				rectElement.setAttribute('class', stripEventClass);
				rectElement.setAttribute('width', 1);
				rectElement.setAttribute('height', 8);
				d.eventIndicator = rectElement;
				return rectElement;
			} else {
				var points = [];
				points.push(2);
				points.push(8);
				points.push(2);
				points.push(0);
				points.push(d.groupEvents[d.eventCount - 1].x - d.x + 2);
				points.push(0);
				points.push(d.groupEvents[d.eventCount - 1].x - d.x + 2);
				points.push(8);
				stripEventClass = stripEventClass += ' grouped-event';
				var polylineElement = document.createElementNS(ns, 'polyline');
				polylineElement.setAttribute('class', stripEventClass);
				polylineElement.setAttribute('points', points.join());
				return polylineElement;
			}
		});

	// remove existing icons so can be redrawn with group icon as necessary
	canvas
		.selectAll('svg.event-icon')
		.selectAll('*')
		.remove();

	const iconBgOffset = 4; //the gap between the icon and the background border
	const iconBgSize = 15 + iconBgOffset * 2; //15=icon size + twice the offset
	const iconBgCenterOffset = iconBgSize / 2 - iconBgOffset; //center coordiantes for the background circle: half the size - the offset
	const iconBgStrokeWidth = 1; //border size for the background circle
	const viewBox = (
		`${-iconBgOffset - iconBgStrokeWidth} `.repeat(2) +
		`${iconBgSize + 2 * iconBgStrokeWidth} `.repeat(2)
	).trim();

	const calcIconX = (d) => d.x - 9 + d.width / 2 - iconBgOffset;

	const getBadgeWidth = (d) => {
		//adapting group badge width to number of digits, supporing up to 3 characters.
		const value = d.eventCount + 1;
		return value > 99 ? 25 : 18;
	};

	/**
	 * Calculating the X offset of the group badge.
	 * Making sure it's not cut-off when near the timeline borders.
	 */

	const calcGroupIconX = (d) => {
		const totalWidth = get(state, 'timelineRef.current.clientWidth', 0);
		const estimation = d.x - 9 + d.width / 2;
		const badgeWidth = getBadgeWidth(d);

		return Math.max(0, Math.min(totalWidth - badgeWidth, estimation)) || 0;
	};

	//event icons: general update for all
	const eventIconSelect = canvas
		.selectAll('svg.event-icon')
		.data(events)
		.classed('group', (d) => d.iconName === 'group')
		.style('visibility', (d) => d.visibility)
		.classed('hover', (d) => d.hover)
		.classed('selected', (d) => d.selected)
		.classed('highlighted', (d) => d.highlighted);

	//selection of non-group icons
	const nonGroupEvents = eventIconSelect
		.filter(':not(.group)')
		.attr('x', calcIconX)
		.attr('y', config.iconsY - iconBgOffset)
		.style('color', (d) =>
			//reset icon color when an event is in special state. Icon color for this states is defined in CSS (a11y: contrast ratio).
			d.selected || d.highlighted || d.hover ? null : d.iconColor
		)
		.attr('viewBox', viewBox)
		.attr('height', iconBgSize)
		.attr('width', iconBgSize);

	//background for non-group icons
	nonGroupEvents
		.append('circle')
		.classed('icon-background', true)
		.attr('r', iconBgSize / 2)
		.attr('cx', iconBgCenterOffset)
		.attr('cy', iconBgCenterOffset);

	nonGroupEvents.append((d) => getChartIconSvg(d.iconName, state.iconMap));

	//selection of group icons
	const groupEvents = eventIconSelect
		.filter('.group')
		.attr('viewBox', null)
		.attr('height', null)
		.attr('width', null)
		.style('color', null)
		.attr('x', calcGroupIconX)
		.attr('y', config.iconsY - 4);

	//now-badge for group icons
	groupEvents.append((d) => {
		var ns = 'http://www.w3.org/2000/svg';
		var foreignObject = document.createElementNS(ns, 'foreignObject');
		const status =
			d.hover || d.selected ? 'info' : d.highlighted ? 'warning' : 'low';
		const value = d.eventCount + 1;
		const variant = d.hover || d.selected ? 'primary' : 'secondary';
		let spanElement = document.createElement('span');
		spanElement.innerHTML = `<now-badge round size='sm' status='${status}' value='${value}' variant='${variant}'/>`;
		foreignObject.appendChild(spanElement);
		//adapt width to number of digits, supporing up to 3 characters.
		foreignObject.setAttribute('width', getBadgeWidth(d));
		foreignObject.setAttribute('height', 18);
		return foreignObject;
	});

	//Fixing offset and size of hotspot object to cover the entire event entity.
	const hotspots = canvas
		.selectAll('rect.timeline-event-hotspot')
		.data(events)
		.style('visibility', (d) => d.visibility);

	//hotspots for non-group events
	hotspots
		.filter((d) => d.iconName !== 'group')
		.attr('width', iconBgSize + 2)
		.attr('x', (d) => calcIconX(d) - 2);

	//hotspots for group events
	hotspots
		.filter((d) => d.iconName === 'group')
		.attr('width', (d) => getBadgeWidth(d) + 2)
		.attr('x', (d) => calcGroupIconX(d) - 2);

	// Move Ranges
	canvas
		.selectAll('rect.timeline-ranges')
		.data(ranges)
		.attr('x', (d) => {
			return d.x;
		})
		.attr('aria-pressed', (d) => {
			return d.selected ? 'true' : 'false';
		})
		.attr('filter', (d) => {
			if (!d.hidden && d.hover) {
				return `url(${document.location.href}#drop-shadow-timeline)`;
			}
			return null;
		})
		.style('visibility', (d) => {
			return d.hidden ? 'hidden' : 'visible';
		})
		.attr('tabindex', function(d) {
			if (d.x + d.width > 5 && d.x < chartWidth - 5) {
				return 0;
			}
			return -1;
		})
		.attr('focusable', function(d) {
			// required for tab key to work in IE11 and Edge
			if (d.x + d.width > 2 && d.x < chartWidth - 2) {
				return true;
			}
			return false;
		})
		.attr('width', (d) => {
			return d.width;
		});

	// Move Range Selectors
	canvas
		.selectAll('rect.rangeselectors')
		.data(ranges)
		.attr('x', (d) => {
			return d.x;
		})
		.style('visibility', (d) => {
			return d.selected && !d.hidden ? 'visible' : 'hidden';
		})
		.attr('width', (d) => {
			return d.width;
		});

	// set the visible date values based on current zoom state
	config.currentViewStart = newXScale.invert(0);
	config.currentViewEnd = newXScale.invert(svg.attr('width'));

	// Note: it seems that the events and ranges are updated without requiring a dispatch
	// dispatch(LOCALS_SET, {events: events, ranges: ranges});
};

const resizeChart = (timelineRef, svg, xScale, xAxis, zoom, config) => {
	// set svg to 0 width so that outer div can resize correctly.
	svg.attr('width', 0);
	// get the width of the outer div. Width must be set at 1 or higher to prevent d3 calculations from failing.
	const width =
		timelineRef.current.clientWidth > 0 ? timelineRef.current.clientWidth : 1;

	// set new width of SVG
	svg.attr('width', width);

	// reset d3 values
	xScale.range([0, width]);
	xAxis.scale(xScale);
	svg.select('.x.axis').call(xAxis);
	zoom.translateExtent([
		[xScale(new Date(config.minDate)), 10000],
		[xScale(new Date(config.maxDate)), 10000]
	]);

	// resize the strip
	const strip = svg.select('rect.timeline-strip');
	strip.attr('width', width);

	const displayScale = d3.zoomIdentity
		.scale(
			width / (xScale(config.currentViewEnd) - xScale(config.currentViewStart))
		)
		.translate(-xScale(config.currentViewStart), 0);

	displayScale.callbackFn = null;
	svg
		.transition()
		.duration(0)
		.call(zoom.transform, displayScale);
};

const panDurationUpdate= (panDuration, dispatch) => {
	const momentDuration = moment.duration(panDuration);
	let panDurationNum = "";
	let unit = "";
	if (momentDuration.asMilliseconds() < 100) {
		panDurationNum = Math.floor(momentDuration.asMilliseconds());
		unit = panDurationNum > 1 ? t("milliseconds") : t("millisecond");
	} else if (momentDuration.asSeconds() < 60) {
		panDurationNum = Math.floor(momentDuration.asSeconds());
		unit = panDurationNum > 1 ? t("seconds") : t("second");
	} else if (momentDuration.asMinutes() < 60) {
		panDurationNum = Math.floor(momentDuration.asMinutes());
		unit = panDurationNum > 1 ? t("minutes") : t("minute");
	} else if (momentDuration.asHours() < 24) {
		panDurationNum = Math.floor(momentDuration.asHours());
		unit = panDurationNum > 1 ? t("hours") : t("hour");
	} else if (momentDuration.asDays() < 30) {
		panDurationNum = Math.floor(momentDuration.asDays());
		unit = panDurationNum > 1 ? t("days") : t("day");
	} else if (momentDuration.asMonths() < 12) {
		panDurationNum = Math.floor(momentDuration.asMonths());
		unit = panDurationNum > 1 ? t("months") : t("month");
	} else {
		panDurationNum = Math.floor(momentDuration.asYears());
		unit = panDurationNum > 1 ? t("years") : t("year");
	}

	dispatch(TIMELINE_CHART_CALCULATION, {
		panDuration: panDurationNum + " " + unit
	});
};

/**
 * The chart is the d3 display of events and ranges in chronological order,
 *
 * The timeline is generic and flexible in order to accommodate for various use cases.
 * A timeline can include event indicators and/or range indicators; and can be either interactive or show a fixed view of time and events.
 *
 * ```jsx
 * <timeline-chart minDate="1546318800000" maxDate="1554091200000" visibleStart="1548997200000" visibleEnd="1552622400000" allowZoomPan="true" maxZoomMilli="D" eventData={[
 *   {name: 'critical', icon: 'circleExclamationFill', color: ''#ff0000', label: 'Critical event', labelPlural: 'Critical events', events: [
 *     {timestamp: 1549111622099, description: 'Critical event #1', data: {}},
 *     {timestamp: 1549344808283, description: 'Critical event #2, data: {}}
 *   },
 *   {name: 'minor', icon: 'circleExclamationFill', color: ''#fcc742', label: 'Minor event', labelPlural: 'Minor events', events: [
 *     {timestamp: 1552188356453, description: 'Minor event #1', data: {}},
 *     {timestamp: 1547477675493, description: 'Minor event #2, data: {}},
 *     {timestamp: 1547334241375, description: 'Minor event #3, data: {}}
 *   },
 * ]}></timeline-chart>
 *
 * @seismicElement timeline-chart
 * @summary D3 chart used to display event data for sn-component-timeline.
 */
createCustomElement('timeline-chart', {
	styles,
	renderer: {
		view: view
	},
	initialState: {
		host: {},
		addResizeListener: {},
		removeResizeListener: {},
		timelineRef: {},
		tooltipRef: {},
		svgRef: {},
		zoomObj: {},
		gX: {},
		xAxis: {},
		xScale: {},
		config: {},
		events: [],
		ranges: [],
		propsObj: {},
		iconMap: {}
	},
	properties: {
		/**
		 * The text should render from right to left
		 * @type {boolean}
		 */
		rtl: {
			default: false
		},
		/**
		 * The minimum date that can be displayed in the timeline as UTC timestamp.
		 * @type {number}
		 */
		minDate: {
			default: 0
		},
		/**
		 * The maximum date that can be displayed in the timeline as UTC timestamp.
		 * @type {number}
		 */
		maxDate: {
			default: 0
		},
		/**
		 * Optional starting date displayed on the timeline as UTC timestamp.
		 * minDate will be used if not supplied.
		 * @type {number}
		 */
		visibleStart: {
			default: -1
		},
		/**
		 * Optional ending date displayed on the timeline as UTC timestamp.
		 * maxDate will be used if not supplied.
		 * @type {number}
		 */
		visibleEnd: {
			default: -1
		},
		eventData: {
			default: []
		},
		rangeData: {
			default: []
		},
		/**
		 * Hides the pan and zoom buttons from the timeline display.
		 * @type {boolean}
		 */
		allowZoomPan: {
			default: true
		},
		/**
		 * The percentage used to calculate the amount of zoom for each zoom action.
		 * @type {number}
		 */
		zoomStepPercent: {
			default: 20
		},
		/**
		 * The percentage used to calculate the amount of pan for each pan action.
		 * @type {number}
		 */
		panStepPercent: {
			default: 20
		},
		/**
		 * The maximum display range that is allowed for the zoom in action. Y=Year, M=Month, D=Day, H=Hour, N=Minute, S=Second, MS=Millisecond
		 * Default is a day
		 * @type {string}
		 */
		maxZoomMilli: {
			default: 'D'
		},

		/**
		 * When true, the timeline will show in one line, the legend and the title won't be displayed.
		 * @type {boolean}
		 */
		isCompactView: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},

		/**
		 * If true, then clicking on range/event will only dispatch the action `TIMELINE_CHART#DATA_SELECTED`.
		 * The actual selection is performed from input properties `selectedEvents` and `selectedRanges`.
		 *
		 * `manage-selections` feature required each event and each range to have data with unique id field: range.data.id / event.data.id
		 * The uniqueness should be ensured within array of same type items (ranges/ events).
		 * @type {boolean}
		 */
		manageSelections: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},

		/**
		 * This property is active only when `manageSelections=true`.
		 * The format: an array of timeline event objects in the same format as provided by action `TIMELINE_CHART#DATA_SELECTED`.
		 * All events in this array will be visually selected on the timeline.
		 * @type {array}
		 */
		selectedEvents: {
			default: [],
			schema: {
				type: 'array'
			}
		},

		/**
		 * This property is active only when `manageSelections=true`.
		 * The format: an array of timeline range objects in the same format as provided by action `TIMELINE_CHART#DATA_SELECTED`.
		 * All ranges in this array will be visually selected on the timeline.
		 * @type {array}
		 */
		selectedRanges: {
			default: [],
			schema: {
				type: 'array'
			}
		},

		/**
		 * Array of ids of events to highlight on the tiemline.
		 * Highlighting is only supported for events.
		 * Highlighted event has to have a unique id in event.data.id.
		 * @type {array}
		 */
		highlightedEventIds: {
			default: [],
			schema: {
				type: 'array'
			}
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_RENDERED]({state, updateState, dispatch}) {
			if (state.visiblityObserverTimerId) {
				clearTimeout(state.visiblityObserverTimerId);
			}
			function setupTimer(updateState, isHidden = false) {
				const timerId = setTimeout(
					() => {
						updateState(({state}) => {
							checkWidthChanges(state, updateState);
							return {
								shouldUpdate: true,
								shouldRender: false
							};
						});
					},
					isHidden ? 1000 : 50
				);
				updateState(() => {
					return {
						operation: 'assign',
						path: 'visiblityObserverTimerId',
						value: timerId,
						shouldUpdate: true,
						shouldRender: false
					};
				});
			}
			function checkWidthChanges(state, updateState) {
				/**
				 * @type {HTMLDivElement}
				 */
				const layoutDiv = state.host.shadowRoot.querySelector(
					'.timeline-chart-layout'
				);
				if (!layoutDiv) {
					return;
				}
				if (getComputedStyle(layoutDiv).visibility === 'hidden') {
					setupTimer(updateState, true);
					return;
				}
				const svgDiv = layoutDiv.firstElementChild;
				if (!svgDiv) {
					return;
				}
				const layoutContainerWidth = parseInt(
					layoutDiv.getBoundingClientRect().width
				);
				if (layoutContainerWidth === 0 || !state.timelineRef.current) {
					setupTimer(updateState);
					return;
				}
				const svgDivWidth = parseInt(svgDiv.getBoundingClientRect().width);
				if (svgDivWidth !== layoutContainerWidth) {
					dispatch(TIMELINE_DO_CHART_RESIZE);
				}
			}
			checkWidthChanges(state, updateState);
		},
		[COMPONENT_DOM_READY]({
			updateState,
			action: {
				payload: {host}
			}
		}) {
			// We need access to host in our action handlers and this is the best way to get that right now given limitations of Seismic
			updateState({host: host});
		},
		[COMPONENT_DISCONNECTED]({
			state,
			dispatch,
			action: {
				payload: {host}
			}
		}) {
			if (state.visiblityObserverTimerId) {
				clearTimeout(state.visiblityObserverTimerId);
			}
			dispatch(TIMELINE_CHART_REMOVE_RESIZE_LISTENER, {host: host});
		},
		TIMELINE_RUN_CHART_ZOOM: {
			private: true,
			stopPropagation: true,
			effect: ({action, state, properties}) => {
				const {svgRef, gX, xAxis, xScale, config} = state;
				const svg = d3.select(svgRef.current);
				const zoomEvent =
					action.payload && action.payload.zoomEvent
						? action.payload.zoomEvent
						: d3.event;
				zoomChart({
					zoomEvent,
					svg,
					gX,
					xAxis,
					xScale,
					config,
					state,
					properties
				});
			}
		},
		TIMELINE_DO_CHART_RESIZE: {
			private: true,
			stopPropagation: true,
			effect: ({state, dispatch}) => {
				const {timelineRef, svgRef, xScale, xAxis, zoomObj, config} = state;
				const svg = d3.select(svgRef.current);
				resizeChart(timelineRef, svg, xScale, xAxis, zoomObj, config, dispatch);
			}
		},
		TIMELINE_DO_CHART_RESET: {
			private: true,
			stopPropagation: true,
			effect: ({state, properties, dispatch, updateState}) => {
				const {host, tooltipRef, svgRef, events, ranges} = state;
				const componentId =
					host && typeof host.getAttribute === 'function'
						? host.getAttribute('component-id')
						: '';
				const tooltipId = `${componentId}_timeline_tooltip`;
				handleChartUpdate({
					svgRef,
					tooltipRef,
					tooltipId,
					state,
					properties,
					dispatch,
					updateState
				});
				updateState({events: events, ranges: ranges});
			}
		},
		TIMELINE_HOVER_CHART_ELEMENT: {
			private: true,
			stopPropagation: true,
			effect: ({action, state, dispatch, properties}) => {
				const {events, ranges, svgRef, tooltipRef, config} = state;
				const {isCompactView} = properties;
				const {data, show} = action.payload;

				// ensure hover is off for all events and ranges. D3 mouse scrolling caused some hover elements to orphan
				events.forEach((event) => {
					event.hover = false;
				});

				ranges.forEach((range) => {
					range.hover = false;
				});

				data.hover = show;

				if (tooltipTimer) {
					clearTimeout(tooltipTimer);
				}

				if (show) {
					// prepare tooltip for display
					setTooltip({svgRef, tooltipRef, data, isCompactView});
					dispatch(TIMELINE_SET_CHART_TOOLTIP_TIMER);
				} else {
					dispatch(TIMELINE_DELETE_CHART_TOOLTIP_TIMER);
				}
				// track that this event is caused by a hover so won't dispatch any range change events
				config.isHoverEvent = show;
				dispatch(TIMELINE_CHART_STATIC_RENDER);
			}
		},
		TIMELINE_SET_CHART_TOOLTIP_TIMER: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				const {tooltipRef} = state;
				// Set timer to show tooltip
				setTooltipTimer(tooltipRef);
			}
		},
		TIMELINE_DELETE_CHART_TOOLTIP_TIMER: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				const {tooltipRef} = state;
				hideTooltip(tooltipRef);
			}
		},
		TIMELINE_CHART_STATIC_RENDER: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				const {svgRef, zoomObj} = state;
				const svg = d3.select(svgRef.current);
				const transform = JSON.parse(
					JSON.stringify(d3.zoomTransform(svg.node()))
				);

				// call render function without actually changing the scale
				svg.call(
					zoomObj.transform,
					d3.zoomIdentity.translate(transform.x, transform.y).scale(transform.k)
				);
			}
		},
		TIMELINE_CHART_INIT_RESIZE_LISTENER: {
			private: true,
			stopPropagation: true,
			effect: ({state, dispatch}) => {
				state.addResizeListener(state.host, dispatch);
			}
		},
		TIMELINE_CHART_REMOVE_RESIZE_LISTENER: {
			private: true,
			stopPropagation: true,
			effect: ({state}) => {
				state.removeResizeListener(state.host);
			}
		},
		TIMELINE_CHART_ZOOM: {
			stopPropagation: true,
			effect: ({action, state, dispatch}) => {
				const {svgRef, zoomObj} = state;
				const svg = d3.select(svgRef.current);
				const dir = action.payload.dir || TIMELINE_ZOOM_IN;

				// make a deep copy of the current transform otherwise object keeps pointers to x and y
				const transform = JSON.parse(
					JSON.stringify(d3.zoomTransform(svg.node()))
				);

				const scaleStep = (transform.k * action.payload.step) / 100;
				const newScale =
					dir === TIMELINE_ZOOM_IN
						? transform.k + scaleStep
						: transform.k - scaleStep;

				// hack here to force scale to avoid flash on transition with duration
				zoomObj.scaleTo(svg, transform.k);

				// example - https://gist.github.com/tomeustace/64b59a7b6c269ceaaffdba0b987375c8
				svg
					.transition()
					.duration(300)
					.call(zoomObj.scaleTo, newScale);

				// use the width of the strip for calculating step
				const width = svg
					.select('rect.timeline-strip')
					.node()
					.getBBox().width;
	
				// set step at 20% of width of chart then divide by the current scale
				const step = (width * (state.properties.panStepPercent / 100)) / newScale;

				const panDuration = (state.properties.maxDate - state.properties.minDate) * step / width;
				panDurationUpdate(panDuration, dispatch);
			}
		},
		TIMELINE_CHART_PAN: {
			stopPropagation: true,
			effect: ({action, state, dispatch}) => {
				const {svgRef, zoomObj} = state;
				const svg = d3.select(svgRef.current);
				const dir = action.payload.dir || TIMELINE_NEXT_DATE;

				// make a deep copy of the current transform otherwise object keeps pointers to x and y
				const transform = JSON.parse(
					JSON.stringify(d3.zoomTransform(svg.node()))
				);

				// use the width of the strip for calculating step
				const width = svg
					.select('rect.timeline-strip')
					.node()
					.getBBox().width;

				// set step at 20% of width of chart then divide by the current scale
				const step = (width * (action.payload.step / 100)) / transform.k;
				// hack here to force scale to avoid flash on transition with duration
				zoomObj.scaleTo(svg, transform.k);

				// example - https://gist.github.com/tomeustace/64b59a7b6c269ceaaffdba0b987375c8
				svg
					.transition()
					.duration(300)
					.call(
						zoomObj.translateBy,
						dir === TIMELINE_NEXT_DATE ? -step : step,
						0
					);

				const panDuration = (state.properties.maxDate - state.properties.minDate) * step / width;
				panDurationUpdate(panDuration, dispatch);
			}
		},
		TIMELINE_CHART_TOGGLE_EVENT: {
			stopPropagation: true,
			effect: ({action, state, dispatch}) => {
				// loop through the events and toggle the visible flag based on payload
				const {name, visible} = action.payload;
				const {events} = state;

				events.forEach((eventItem) => {
					if (eventItem.name === name) {
						eventItem.hidden = !visible;
					}
				});

				// Note: it seems that the events on locals are updated and a dispatch is not needed
				// redraw the chart
				dispatch(TIMELINE_CHART_STATIC_RENDER);
			}
		},
		TIMELINE_CHART_TOGGLE_RANGE: {
			stopPropagation: true,
			effect: ({action, state, dispatch}) => {
				// loop through the ranges and toggle the visible flag based on payload
				const {name, visible} = action.payload;
				const {ranges} = state;

				ranges.forEach((rangeItem) => {
					if (rangeItem.name === name) {
						rangeItem.hidden = !visible;
					}
				});

				// Note: it seems that the ranges on locals are updated and a dispatch is not needed
				// redraw the chart
				dispatch(TIMELINE_CHART_STATIC_RENDER);
			}
		},

		[TIMELINE_CHART_EVENT_SELECT]: {
			private: true,
			stopPropagation: true,
			effect: effectSelectItems
		},

		[TIMELINE_CHART_RANGE_SELECT]: {
			private: true,
			stopPropagation: true,
			effect: effectSelectItems
		},

		[TIMELINE_ON_ZOOM_END]: {
			private: true,
			stopPropagation: true,
			effect: onZoomEnd
		}
	}
});
