import moment from 'moment';

export const getRangeDisplay = (item) => {
	// TODO: THIS MAY NEED TO CHANGE AS DURATION MAY NOT REALLY EXPAND MUTLIPLE RANGES
	// calculate the total difference for each range in milliseconds
	let totalDuration = 0;

	item.ranges.forEach((range) => {
		const start = new moment(range.start);
		const end = new moment(range.end);
		const duration = moment.duration(end.diff(start));
		totalDuration += duration.as('milliseconds');
	});

	// get a total time span from now plus the total duration
	const current = moment();
	const span = moment().add(totalDuration, 'ms');

	// return the difference as a friendly string
	return span.to(current, true);
};