import {createCustomElement} from '@servicenow/ui-core';
import styles from './_timeline-legend-item.scss';

// external components
import '@servicenow/now-icon';
import '@servicenow/now-toggle';
import {t} from 'sn-translate';

// constants
import {
	TIMELINE_LEGEND_TOGGLED,
	TIMELINE_LEGEND_ITEM_UPDATE
} from '../common/timeline-constants';
import {getRangeDisplay} from '../utilities/ultilities';

/** @seismicView timeline-legend-item */
const view = (state) => {
	// props

	const {item, index, isRangeItem, hideToggle} = state.properties;
	const {isOn} = state;

	const iconDisplay = isRangeItem ? (
		<div
			className="legend-range-color"
			style={isOn ? {background: item.color} : ''}
		/>
	) : (
		<now-icon
			icon={item.icon}
			size="md"
			className="legend-item-icon"
			style={isOn ? {color: item.color} : ''}
		/>
	);

	const countDisplay = isRangeItem
		? t('{0}', getRangeDisplay(item))
		: item.events.length;
	const toggleCheckedAttr = isOn ? {checked: true} : {};

	const toggleDispaly = hideToggle ? (
		''
	) : (
		<now-toggle
			{...toggleCheckedAttr}
			size="sm"
			config-aria={{'aria-label': t('Toggle {0} display', item.label)}}
		/>
	);

	// main component display
	return (
		<div
			class={{
				'legend-item': true,
				'now-p-block--sm': true,
				'legend-item-first': index === 0,
				'legend-item-off': !isOn
			}}>
			{iconDisplay}
			<div
				className="legend-item-label now-p-inline--sm"
				aria-label={t('{0}', item.label)}>
				{item.label}
			</div>
			<div
				className="legend-item-count now-p-inline-end--sm"
				aria-label={t('{0}', countDisplay)}>
				{countDisplay}
			</div>
			<div className="now-p-inline-end--md">{toggleDispaly}</div>
		</div>
	);
};

/**
 * Displays a formatted row of legend data for a specific event or range data group.
 *
 * ```jsx
 * const eventItem = {name: 'critical', icon: 'circleExclamationFill', color: ''#ff0000', label: 'Critical event', labelPlural: 'Critical events', events: [
 *   {timestamp: 1549111622099, description: 'Critical event #1', data: {}},
 *   {timestamp: 1549344808283, description: 'Critical event #2, data: {}}
 * ]};
 * <timeline-legend-item item={eventItem} index=0></timeline-legend-item>
 *
 * const rangeItem = {name: 'RangeA', color: '#F95070', label: 'Range type A', ranges: [
 *   {start: 1549152000000, end: 1549497600000, description: 'Range type A #1', data: {}},
 *   {start: 1549584000000, end: 1550275200000, description: 'Range type A #2', data: {}}
 * ]};
 * <timeline-legend-item item={rangeItem} index=1 is-range-item></timeline-legend-item>
 * ```
 *
 * @seismicElement timeline-legend-item
 * @summary Displays a formatted row of legend data for a specific event or range data group.
 */
createCustomElement('timeline-legend-item', {
	styles,
	renderer: {
		view: view
	},
	initialState: {
		isOn: true
	},
	properties: {
		/**
		 * Object that defines the event or range type to be displayed for the legend item.
		 * Each object must have a unique name.
		 * The icon must be taken from the gallery of available eds icons.
		 * The color used for the icon must be a full hex string.
		 * The data object can be any valid json specific to the particular event or range.
		 * @type {{name: string, icon: (string|undefined), color: string, label: string, labelPlural: (string|undefined), events: (Object|undefined), ranges: (Object|undefined)}}
		 */
		item: {
			default: {},
			onChange(current, previous, dispatch) {
				// always start with the toggle being on for a new item
				dispatch(TIMELINE_LEGEND_ITEM_UPDATE, {isOn: true});
			}
		},
		/**
		 * The index of the data being displayed on the legend item.
		 * Data is generally part of an array and mapped into the legend item.
		 * @type {number}
		 */
		index: {
			default: -1
		},
		/**
		 * Whether the data supplied is a range item.
		 * @type {boolean}
		 */
		isRangeItem: {
			default: false
		},
		/**
		 * Disables the toggle from the timeline legend display.
		 * @type {boolean}
		 */
		hideToggle: {
			default: false
		}
	},
	actionHandlers: {
		TIMELINE_LEGEND_ITEM_UPDATE: {
			private: true,
			stopPropagation: true,
			effect: ({action, updateState}) => {
				const {isOn} = action.payload;
				updateState({isOn: isOn});
			}
		},
		'NOW_TOGGLE#CHECKED_SET': {
			private: true,
			stopPropagation: true,
			effect: ({action, state, dispatch, updateState}) => {
				updateState({isOn: action.payload.value});

				// throw event up the chain of the legend
				dispatch(TIMELINE_LEGEND_TOGGLED, {
					item: state.properties.item,
					show: action.payload.value,
					dataType: state.properties.isRangeItem ? 'range' : 'event'
				});
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the legend item display toggle is clicked.
		 * @type {{item: Object}, show: boolean, dataType: string}
		 */
		'TIMELINE_LEGEND#LEGEND_TOGGLED': {}
	}
});
