import {createCustomElement} from '@servicenow/ui-core';
import styles from './_timeline-empty-state.scss';
import {t} from 'sn-translate';

// external components
import '@servicenow/now-icon';

/** @seismicView timeline-empty-state */
const view = (state) => {
	const {
		message,
		isEmpty,
		isError,
		isLoading,
		iconName,
		statusMessage
	} = state.properties;

	const statusIcon = (
		<now-icon
			icon={iconName}
			spin={isLoading}
			size="lg"
			class={{
				'empty-icon': isEmpty,
				'error-icon': isError,
				'loading-icon': isLoading
			}}
		/>
	);

	return (
		<div className="empty-state-layout">
			<div className="centering-container">
				<div className="icon-container">{statusIcon}</div>
				<div className="status-container now-p-block-start--sm">
					{statusMessage}
				</div>
				<div className="message-container now-p-block-start--sm">{message}</div>
			</div>
		</div>
	);
};

createCustomElement('timeline-empty-state', {
	styles,
	renderer: {
		view: view
	},
	properties: {
		isEmpty: '',
		isError: '',
		isLoading: '',
		message: {
			default: ''
		},
		statusMessage: {
			computed({properties: {isEmpty, isLoading}}) {
				if (isLoading) {
					return t('Loading');
				}
				if (isEmpty) {
					return t('No content available');
				}

				return t('Loading Error');
			}
		},
		iconName: {
			computed({properties: {isEmpty, isLoading}}) {
				if (isLoading) {
					return 'loader-outline';
				}
				if (isEmpty) {
					return 'circle-exclamation-outline';
				}

				return 'triangle-exclamation-outline';
			}
		}
	}
});
