import {createCustomElement} from '@servicenow/ui-core';
import styles from './_timeline-legend-title.scss';

// external components
import {t} from 'sn-translate';

/** @seismicView timeline-legend-title */
const view = (state) => {
	// props
	const {legendTitle} = state.properties;

	// main component display
	return (
		<div role="table" aria-label={t('Legend')} className="now-p-block-end--xs">
			<div role="rowgroup">
				<div role="row">
					<span
						role="rowheader"
						className="legend-title"
						aria-label={legendTitle}>
						{legendTitle}
					</span>
				</div>
			</div>
		</div>
	);
};

/**
 * Displays a formatted title for the group of events or ranges being displayed on the timeline legend.
 *
 * ```jsx
 * <timeline-legend-title legendTitle="Timeline events"></timeline-legend-title>
 * ```
 *
 * @seismicElement timeline-legend-title
 * @summary Displays a formatted title for the group of events or ranges being displayed on the timeline legend.
 */
createCustomElement('timeline-legend-title', {
	styles,
	renderer: {
		view: view
	},
	properties: {
		/**
		 * The title used for events in the legend.
		 * @type {string}
		 */
		legendTitle: {
			default: t('Legend')
		}
	}
});
